import React, { Component } from "react";
import Form from "react-bootstrap/Form";
import {cfg} from "./Cfg.js";

class RadioSortDir extends Component {
    constructor(props) {
		super(props);
		this.onChange = this.onChange.bind(this);
	}
	render() {
		return (
			<Form>
			    <Form.Check 
					type="radio"
					label="ascending"
					id="ascending"
					value="ascending"
					checked={cfg.sortAsc}
					onChange={this.onChange}
					disabled={this.props.disabled}
				/>
				<Form.Check
					type="radio"
					label="descending"
					id="descending"
					value="descending"
					checked={!cfg.sortAsc}
					onChange={this.onChange}
					disabled={this.props.disabled}
				/>
			</Form>
		);
	}
	onChange(e) {
		cfg.sortAsc = (e.currentTarget.value === "ascending");
		this.forceUpdate();
		if (this.props.onChanged === undefined)
			alert("Error in component RadioSortDir: undefined callback function onChanged")
		else				
			this.props.onChanged(e);
	}
}

export default React.memo(RadioSortDir);