import "./NavBar.css";
import React from "react";
import Navbar from "react-bootstrap/Navbar";
import Container from "react-bootstrap/Container";
import Offcanvas from "react-bootstrap/Offcanvas";
//import Nav from "react-bootstrap/Nav";
//import NavDropdown from "react-bootstrap/NavDropdown";
////import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
//import Form from "react-bootstrap/Form";
//import FormControl from "react-bootstrap/FormControl";
import Button from "react-bootstrap/Button";
//import OverlayTrigger from "react-bootstrap/OverlayTrigger";
//import Popover from "react-bootstrap/Popover";
import ListGroupToggleViewMapOrMeters from "./ListGroupToggleViewMapOrMeters.js";
import { List } from "react-bootstrap-icons";
import ButtonUser from "./ButtonUser.js";
import {cfg, appTitle} from "./Cfg.js";


function NavBar(props) { //title, onBtnOffcanvas, onSelectTab
	let titleBtn1 = cfg.currentTabIdx === 0 ? "object list" : "instruments panel";

	return (
		<Navbar key="NavBar" bg="secondary" variant="dark" expand="sm">
			<Container fluid>
				<div className="for-sm">
					<Button variant="outline-light" className="me-3 header_btn"
							title = {titleBtn1}
							onClick={ () => props.onBtnOffcanvasObjectListOrInstruments() }>
						<b>&#9698;</b>
					</Button>
				</div>
				<img  	title="Glory to Ukraine !"	alt="Glory to Ukraine !"
						src="ukrainian_ribbon.svg"	width="30"	height="30"
						className="d-inline-block align-top"
				/>
				<Navbar.Brand _href="#home" >
					{props.title}
				</Navbar.Brand>
				{ /*
				<Navbar.Toggle className="navbar-toggler-focus" aria-controls={'offcanvasNavbar-expand-sm'} title="menu panel"/>
				*/ }
				<Navbar.Offcanvas	id={"offcanvasNavbar-expand-sm"} style={{minWidth: "fit-content"}}
									aria-labelledby={"offcanvasNavbarLabel-expand-sm"}
									placement="end">
					<Offcanvas.Header closeButton>
						<Offcanvas.Title id={'offcanvasNavbarLabel-expand-sm'}>
							{appTitle}
						</Offcanvas.Title>
					</Offcanvas.Header>
					<Offcanvas.Body>
						<div style={{width: "100%", display: "flex", justifyContent: "center"}}>
							<ListGroupToggleViewMapOrMeters onChanged={props.onSelectTab} 
															currObj={cfg.currentObject} 
															disabled={props.disabled}/>
						</div>
						<ButtonUser onClick={props.showUserInfo}/>
					</Offcanvas.Body>
				</Navbar.Offcanvas>
				<div className="for-sm">
					<Button variant="outline-light" size="sm"	disabled={false} 
							title="menu panel" 		className="navbar-button"
							onClick={ () => props.onBtnOffcanvasTogglerMapOrObject() }> 
            			<List 	className="navbar-button-image"/>
        			</Button>	
				</div>
			</Container>
		</Navbar>		
	)
}

export default React.memo(NavBar);