import React from "react";
import DropDownMonth from './DropDownMonth.js';
import ListGroupToggleMetersCardsOrChart from './ListGroupToggleMetersCardsOrChart.js';
import DropDownSort from './DropDownSort.js';
import RadioSortDir from './RadioSortDir.js';
import EditFilter from './EditFilter.js';
import DropDownSize from './DropDownSize.js';
import { Button } from "react-bootstrap";
import { cfg } from "./Cfg.js";
import { data } from "./Data.js";

function Instruments(props) { //onDataChanged, onViewChanged, onSortChanged, onFilterChanged, onSizeChanged, disabled
	function onShowObjectInfo() {
		if (props.onObjectInfo) {
			props.onObjectInfo(data.objects.indexOf(cfg.currentObj));
		}
	}
	function onMetersDataChanged(month) {
		if (props.onDataChanged) {
			props.onDataChanged();
		}
	}
	function onMetersViewChanged(sort) {
		if (props.onViewChanged) {
			props.onViewChanged();
		}
	}
	function onMetersSortChanged(sort) {
		if (props.onSortChanged) {
			props.onSortChanged();
		}
	}
	function onMetersFilterChanged(filter) {
		if (props.onFilterChanged) {
			props.onFilterChanged(filter);
		}
	}
	function onMetersSizeChanged(sizeIdx) {
		if (props.onSizeChanged) {
			props.onSizeChanged(sizeIdx);
		}
	}
	return(
		<>
		<div className="instrument d-grid" >
			<Button onClick={onShowObjectInfo} disabled={props.disabled}>
				Object Info
			</Button>
		</div>
		<div className="instrument-title" >
			<h5>data interval</h5>
		</div>
		<div className="instrument" >
			<DropDownMonth onChanged={onMetersDataChanged} disabled={props.disabled} />
		</div>
		<div className="instrument-title" >
			<h5>data presentation</h5>
		</div>
		<div className="instrument" >
			<DropDownSort onChanged={onMetersSortChanged} disabled={props.disabled}/>
		</div>
		<div className="instrument instrument-idented">
			<RadioSortDir onChanged={onMetersSortChanged} disabled={props.disabled}/>
		</div>
		<div className="instrument" >
			<EditFilter onChanged={onMetersFilterChanged} value={props.filter} disabled={props.disabled}/>
		</div>
		<div className="instrument-title" >
			<h5>view settings</h5>
		</div>
		<div className="instrument" >
			<ListGroupToggleMetersCardsOrChart onChanged={onMetersViewChanged} disabled={props.disabled}/>
		</div>
		<div className="instrument" >
			<DropDownSize onChanged={onMetersSizeChanged} disabled={props.disabled}/>
		</div>
		</>
	)

}
			
//export default Instruments;
export default React.memo(Instruments)
