import React, { Component } from "react";
import ListGroup from "react-bootstrap/ListGroup";
import {metersView, cfg} from "./Cfg.js";

class ListGroupToggleMetersCardsOrChart extends Component {
    constructor(props) {
		super(props);
		this.onClick = this.onClick.bind(this);
	}
	render() {
		return (
			<ListGroup horizontal>
				<ListGroup.Item action value={metersView[0]} active={cfg.viewIdx === 0} disabled={this.props.disabled}
								onClick={this.onClick} >
					{metersView[0]}
				</ListGroup.Item>
				<ListGroup.Item action value={metersView[1]} active={cfg.viewIdx === 1} disabled={this.props.disabled}
								onClick={this.onClick} >
					{metersView[1]}
				</ListGroup.Item>
			 </ListGroup>
		)
	}
	onClick(e) {
		let i = metersView.indexOf(e.currentTarget.value);
		if (i !== cfg.viewIdx) {
			if (i >= metersView.length)
				alert("Error in component ListGroupToggleMetersCarsOrChart: error selected value " + e.currentTarget.value)
			else {
				cfg.viewIdx = i;
				this.forceUpdate();
				if (this.props.onChanged === undefined)
					alert("Error in component ListGroupToggleMetersCarsOrChart: undefined callback function onChanged")
				else				
					this.props.onChanged(e);
			};
		};
	}
}
export default ListGroupToggleMetersCardsOrChart;