import './customToast.css'; //https://stackoverflow.com/questions/53295656/simplest-way-to-adjust-background-color-of-a-react-toastify-toast
import { toast } from 'react-toastify';
//import { css } from 'glamor';

const customToast = {
    success(msg, options = {}) {
        return toast.success(msg, {
            ...options,
            className: 'toast-success-container toast-success-container-after',
            //progressClassName: css( {background: '#34A853'} ),
        });
    },
    error(msg, options = {}) {
        return toast.error(msg, {
            ...options,
            className: 'toast-error-container',
            //progressClassName: css( {background: '#EE0022',} ),
        });
    },
    info(msg, options = {}) {
        return toast.info(msg, {
        ...options,
        className: 'toast-info-container',
        //progressClassName: css({background: '#07F',}),
        });
    },
    about(msg, options = {}) {
        return toast.info(msg, {
        ...options,
        className: 'toast-about-container',
        //progressClassName: css({background: '#07F',}),
        });
    }
};

export default customToast;