import React from "react";
import './ListGroupToggleViewMapOrMeters.css';
//import { toast } from "react-toastify";
import { utils } from "./Utils.js";
import ListGroup from "react-bootstrap/ListGroup";
import {cfg} from "./Cfg.js";


function ListGroupToggleViewMapOrMeters(props) { // onChanged - callback , currObj -   -render

	const [objHighlight, setObjHighlight] = React.useState(false);
	let tabIdx = cfg.currentTabIdx;	//const [tabIdx, setTabIdx] = React.useState(cfg.currentTabIdx);
	React.useEffect(	() => 	{
				if (cfg.currentObject && cfg.currentTabIdx === 0) {
				setObjHighlight(true);
				setTimeout(() => {
					setObjHighlight(false);
				}, 100);
			}
		}, [cfg.currentObject]
	); 

	function onClick(e) {
		let i = Number(e.currentTarget.value);
		if (i !== cfg.currentTabIdx) {
			if (i === 1 && cfg.currentObject == null) {
				utils.toastError("please, first select an object", {autoClose: 3000})
			} else {
				cfg.currentTabIdx = i;
				//setTabIdx(i);
				if (props.onChanged === undefined)
					alert("Error in component ListGroupToggleViewMapOrMeters: undefined callback function onChanged")
				else {				
					props.onChanged(e);
				};
			};
		};
	}

	let objectName = cfg.currentObject ? cfg.currentObject.name : "Object not selected";
	const class0 = "ListGroupToggleViewMapOrMetersItem";
	const class1 = cfg.currentObject == null ? class0 + "-disabled" : (objHighlight ? class0 + "-highlighted" : class0);
	return (
		<ListGroup horizontal className="ListGroupToggleViewMapOrMeters">
			<ListGroup.Item action className={class0} title="map with available objects"
							active={0 === tabIdx} disabled={props.disabled}
							onClick={onClick} value={"0"}>
				Objects
			</ListGroup.Item>
			<ListGroup.Item action className={class1} title="meters of the selected object"
							active={1 === tabIdx} disabled={props.disabled}
							onClick={onClick} value={"1"}>
				{objectName}
			</ListGroup.Item>
		</ListGroup>
	)
}

export default ListGroupToggleViewMapOrMeters
//export default React.memo(ListGroupToggleViewMapOrMeters)