import React from 'react';
import CardMeter from './CardMeter.js';
import {data} from './Data.js';
import { cfg } from "./Cfg.js";

function MeterCards(props) {
	if (data.meters.items) {
		let filter = cfg.filter;//props.filter
		return (
			<>
			{data.meters.items.map( function(meter, index) {
				if (!meter.title || meter.title.search(filter) !== -1)
					if (meter.id === -1)
						return(
						<div className="flip" key={meter.id}>
							<CardMeter meter={meter} setStatus={props.setStatus}  unflip={false} close={false} />
						</div>
						)  
					else
					return(
						<div className="flip" key={meter.id}>
							<CardMeter 	meter={meter} setStatus={props.setStatus}  
										unflip={props.metersState.unflipAll} close={props.metersState.closeAll} />
						</div>
					)
				return null;
			})}
			</>
		)
	} else {
		return
	}
}

export default React.memo(MeterCards);
//export default MeterCards
