import React, { Component } from "react";
import { ResponsiveContainer, BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend} from "recharts";
import Spinner from 'react-bootstrap/Spinner';
import { data } from "./Data.js";
import { cfg } from "./Cfg.js";

const charts = [{tarName: "#1", tarIdx: 1}, {tarName: "#2", tarIdx: 2}, {tarName: "#3", tarIdx: 3}];
function setCharts(indexes) {
	charts.length = 0;
	indexes.forEach( (index, idx, indexes) => {
		charts.push( {	tarName: cfg.tariffs[index].name,
						tarIdx: index,
						tarColor: cfg.tariffs[index].color } )
		}
	)
}
class Point {
	constructor(meter, name = "") {
		this.name = meter ? meter.title : name;
		charts.forEach( (chart, idx) => {
							this["tariff " + charts[idx].tarName] = meter ? meter.energy[ charts[idx].tarIdx ] : 0;
						}
					  )
	}
}

const constCritWidth = 700;
class MeterBars extends Component {
	constructor(props) {
		super(props);
		//this.onClick = this.onClick.bind(this);
		this.ref = React.createRef();
		this.updateDimensions = this.updateDimensions.bind(this);
		this.aspect = 1;
		this.resized = false;
		this.width = null;
		this.dataCount = -1;
		this.charts = [];
		this.chartRef = React.createRef();
		this.axisXmax = 0;	
		this.state = { aspect: 1, show: true, data: this.prepareData() };
	}
	setAxisXmax(elem) {
		if ( this.state.closeAll ) {
			this.axisXmax = 0;
		} else {
			if (elem) {
				let a = elem.state.xAxisMap.up.niceTicks;
		  		this.axisXmax = a[a.length-1];
			}
		}
	}
	prepareData(setNull) {
		let result = [];
		if (cfg.sortKindIdx === 3)
			setCharts([2, 3, 1])
		else if (cfg.sortKindIdx === 4)
			setCharts([3, 1, 2])
		else
			setCharts([1, 2, 3]);

		if (setNull) {
			data.meters.items.forEach( 	(meter, index, meters) => {
											if (meter.title.search(cfg.filter) !== -1)
													result.push( new Point(null, meter.title) );
										}
			);
		} else {
			data.meters.items.forEach( 	(meter, index, meters) => {
											if (meter.title.search(cfg.filter) !== -1)
												result.push( new Point(meter) );
										}
			);
		}
		return result;
	}
	calcAspect(width) {
		let h = (window.innerWidth <= constCritWidth ? 24 : 0) + 2*30 + 25 + this.state.data.length*35 + (this.state.data.length-1)*2;
		return width ? width/h : 1;
	}
	updateDimensions() {
		if (this.ref.current) {
			this.width = this.ref.current.offsetWidth;
			this.aspect = this.calcAspect(this.width);
			this.resized = true;
			this.forceUpdate();
		}
	}
	componentDidMount() {
		this.updateDimensions();
		window.addEventListener("resize", this.updateDimensions.bind(this));
		data.onMetersChanged = this.onDataChanged.bind(this);
	}
	componentWillUnmount() {
		window.removeEventListener("resize", this.updateDimensions.bind(this));
		data.onMetersChanged = null;
	}
	onDataChanged(setNull = false, doAfter = undefined) {
		this.setState( {data: this.prepareData(setNull), closeAll: setNull}, doAfter );
	}

	render() {
		if (this.props.netOperation) {
			return(
				<div	ref={this.ref} 	style={	{margin: "auto", height: "100%", position: "relative", inset: "0"} }>
					<Spinner 	animation="border" variant="secondary" 
								style={	{margin: "auto", position: "absolute", inset: "0"} } />
				</div>
			)
		} else {
			if (this.resized)
				this.resized = false;
			let domain = this.axisXmax ? [0, this.axisXmax] : [0, "auto"];
			//let domain_dn = [0, this.axisXmax];
    		let label = this.state.closeAll ? null : renderLabel;
			if (this.dataCount !== this.state.data.length) {
				this.aspect = this.calcAspect(this.width);
				this.dataCount = this.state.data.length;
			}
			//let aspect = this.aspect;
			let t1 = "tariff " + charts[0].tarName;
			let t2 = "tariff " + charts[1].tarName;
			let t3 = "tariff " + charts[2].tarName;
			let c1 = charts[0].tarColor, c2 = charts[1].tarColor, c3 = charts[2].tarColor;
			let legend_layout = window.innerWidth > constCritWidth ? "vertical" : "horizontal"; 
			let legend_align = window.innerWidth > constCritWidth ? "right" : "center";
			return (
				<div  	ref={this.ref} style={ {display:"flex", width:"100%"} } >
				<ResponsiveContainer width="100%" aspect={this.aspect} debounce={1} >
					<BarChart 	ref={this.setAxisXmax.bind(this)} layout="vertical" barCategoryGap="0.1" barSize="200px"
								data={this.state.data} margin={{top: 20, right: 0, left: 0, bottom: 5}} 
					>
						<CartesianGrid strokeDasharray="3 3" horizontal={false} />
						<XAxis 	xAxisId="up" type="number" tickMargin="10" unit=" kWh" domain={domain} 
								orientation="top" _interval="preserveStartEnd" />
						<XAxis 	xAxisId="dn" type="number" tickMargin="10" unit=" kWh" domain={domain} 
								orientation="bottom" _interval="preserveStartEnd" />
						<YAxis yAxisId="lft" type="category" dataKey="name" orientation="left"/>
						<YAxis yAxisId="rgt" tick={false} orientation="right"/>
						
						<Tooltip/>
						<Bar dataKey={t1} xAxisId="up" yAxisId="lft" stackId={t1} fill={c1} isAnimationActive={true}/>
						<Bar dataKey={t2} xAxisId="up" yAxisId="lft" stackId={t1} fill={c2} isAnimationAtive={true}/>
						<Bar dataKey={t3} xAxisId="up" yAxisId="lft" stackId={t1} fill={c3} isAnimationActive={true} label={label} />
						<Legend layout={legend_layout} verticalAlign="top" align={legend_align} top="0"/>
					</BarChart>
				</ResponsiveContainer>
				</div>
			)
		} 
	} 
	//onClick(e) {
	//}
}

const renderLabel = (props) => {
	const { x, y, width, value } = props;
	var text = isNaN(value) ? "?" : value.toFixed(2);// + " kWh";
	return (
		<g>
			<text x={x + width + 10} y={y + 20} textAnchor="left" dominantBaseline="middle">
				{text}
			</text>
		</g>
	  );
};

export default React.memo(MeterBars)
