import './ModalUserAuth.css';
import React from 'react';
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import Spinner from "react-bootstrap/Spinner";
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import {QuestionCircle} from "react-bootstrap-icons";
import {cfg} from './Cfg.js';
import {utils} from './Utils.js';

const 
    cLoginNotAuth = 0,
    cLoginTimeout = 1,
    cLoginConnecting = 2,
    cLoginNotSpecified = 3,
    cLoginSuccess = 4,
    cLoginNotFound = 5,
    cLoginSuccessAfter = 6;
//-----------------------------------------------------------------------------------------------------------------
const UserAccountTitle = React.forwardRef( (props, ref) => {
        const [state, setState] = React.useState( {title: cLoginNotAuth} );    

        React.useImperativeHandle(ref, () => ({
                setTitle: (value) => setState( {title: value} ),
            })
        );
        
        let strAccount = "";
        let clrAccount = "Black";
    
        switch(state.title) {
            case cLoginTimeout:
                strAccount = " needs to be reentered";
                clrAccount = "DarkOrange";
                break;
            case cLoginNotSpecified:
                strAccount = " not specified fully";
                clrAccount = "Red";
                break;
            case cLoginSuccess:
            case cLoginSuccessAfter:
                strAccount = " authenticated successfully";
                clrAccount = "SpringGreen";
                break;
            case cLoginNotFound:
                strAccount = " not found";
                clrAccount = "Red";
                break;
            default:
        }

        return(
            <p>
                User's account <span style={{color: clrAccount}}>{strAccount}</span>
            </p>
        )    
    }
)
//-----------------------------------------------------------------------------------------------------------------
const ButtonAbout = React.forwardRef( (props, ref) => {
        const [state, setState] = React.useState( {active: true} );    

        React.useImperativeHandle(ref, () => ({
                setVisible: (value) => setState( {active: value} ),
            })
        );

        //let caption = state.active ? "?" : "";
        return(
            <Button variant="light" size="sm" disabled={!state.active} onClick={props.onClick}
                    title="about this application"> 
                <h5><QuestionCircle hidden={!state.active}/></h5>
            </Button>
        )
    }
)
//            <b> {caption} </b> 
//-----------------------------------------------------------------------------------------------------------------
function ModalUserAuth(props) { // onLogin, show
    var show = props.show;
    const setShow = (value) => show = value;
	
    const [enabled, setEnabled] = React.useState(true);
    
    const inputName = React.useRef(null);
    const inputPswd = React.useRef(null);
    const btnLogin = React.useRef(null);
    const focused = React.useRef(inputPswd.current);

    let refState = React.useRef(cLoginNotAuth);
    function setState(newState) {
        refState.current = newState;
        setTitleState(refState.current);
    }
    const state = () => {return refState.current};
    
    function setFocus() {
        if (inputName.current.value === "") { // user's name is empty
            inputName.current.focus();
        } 
        else if (inputPswd.current.value === "") { // user's password is empty
            inputPswd.current.focus();
        }
        else {
            if (focused.current) {
                focused.current.focus();
            }
        }
    }

    function close() {
        //setShow(false); //setEnabled(true);
    }

    function login(e) {
        if (e) {
            e.preventDefault();
        };
        let un =  inputName.current.value;
        let up =  inputPswd.current.value;
        if (un === "") { // user's name is empty
            setState(cLoginNotSpecified);
            setFocus();
        } 
        else if (up === "") { // user's password is empty
            setState(cLoginNotSpecified);
            setFocus();
        } else { // user's name and/or password is/are wrong
            utils.toastsClear();
            setState(cLoginConnecting);
            utils.sleep(1500).then(
                () => {
                    //setEnabled(true);
                    if ((un === "demo" && up === "demo")) {
                        cfg.user = [
                            {"name": "demo"}, 
                            {"access rights": "read only"}, 
                            {"account state": "active"},
                            {"account valid from": "01.05.2022"},
                            {"account valid till": "01.05.2025"},
                            {password: "change", component: "button", 
                             hint: "not available for this user", disabled: true}
                        ];
                        if (props.onLogin && props.onLogin.procedure) {
                            setState(cLoginSuccessAfter);
                            props.onLogin.procedure(close, close);
                        } else {
                            setState(cLoginSuccess);
                            utils.sleep(0).then( ()=>{setEnabled(false)} );
                        }
                    } else {
                        setEnabled(true);
                        setShow(true);
                        setState(cLoginNotFound);
                    }
                }
            );          
            setEnabled(false);
        };
    }

    const refUserAccountTitle = React.useRef(null);
    function setTitleState(state) {
        refUserAccountTitle.current.setTitle(state);
    }

    const refButtonAbout = React.useRef(null);
    function setAboutState(state) {
        if (refButtonAbout.current) {
            refButtonAbout.current.setVisible(state);
        }
    }
    
    function onTextChanged(e) {
        if (state() !== cLoginNotAuth) {
            setState(cLoginNotAuth);
            utils.toastsClear();
        }
    }

    function onKeyPress(e) {
        if (e.charCode === 13) {
            btnLogin.current.focus();
            btnLogin.current.click();
        }
    }

    function onFocus(e) {
        switch (e.target.id) {
            case "name":
                focused.current = inputName.current;
                break;
            case "password":
                focused.current = inputPswd.current;
                break;
            default:
        }
    }

    const Msg = ({ closeToast, toastProps }) => ( //<br/>
        <div>
            <p>This is a demo-application intended for use as a basis for building 
            an information system of electricity consumption.</p>
            <p>All the objects on the map are placed randomly and simply numbered instead of assigning individual names. 
            The Obolon' district of Kyiv has been selected due to high density of buildings. 
            Objects' addresses are being extracted by <a href="https://nominatim.openstreetmaps.org">OpenStreetMap service</a>.</p>
            <p>All the electricity accounting data are emulated.</p>
        </div>
      );
    function about(aOptions = {}) {
        utils.toastAbout(Msg, {
                ...aOptions,
                onOpen:     () => {setAboutState(false); setFocus()},
                onClose:    () => {setAboutState(true); setFocus()}
            }
        );
    }

    let shown = React.useRef(false);
    React.useEffect( () => {
            if (show !== shown.current) {
                if (cfg.user) {
                    inputName.current.value = cfg.user.reduce(
                        (res, val) => {return val.name && res === ""? val.name : res}, ""
                    );
                    if (props.show) {
                        //utils.toastsClear();
                        //utils.toastError("user's inactivity timeout", {position: "top-center"});
                    };
                    setState(cLoginTimeout);
                } else {
                    inputName.current.value = "demo";   //
                    if (cfg.demoAuto) {
                        inputPswd.current.value = "demo";
                        utils.toastsClear();
                        login();
                    }
                };
                if (!show && !enabled) {
                    setEnabled(true);
                };
                if (show && !cfg.demoAuto) {
                    about( {delay: 1500} );
                }
            };
            shown.current = show;
            if (show && enabled) {
                setFocus();
            };
        }, [show, enabled] // once upon start
    )

    let disabled = !enabled;
    let message = refState.current === cLoginSuccessAfter && props.onLogin && props.onLogin.message
                    ?   props.onLogin.message + "..."
                    :   "connecting server...";

    return (
        <Modal 	show={show} centered backdrop="static" keyboard={false} enforceFocus restoreFocus
                size="md" fullscreen_="sm-down" aria-labelledby="contained-modal-title-vcenter" >
            <Modal.Header    className="user-auth">
                <Modal.Title id="contained-modal-title-vcenter">
                    <UserAccountTitle   ref={refUserAccountTitle}/>
                    {/*User's account <span style={{color: clrAccount}}>{strAccount}</span>*/}
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form className="inputs">
                    <Form.Group as={Row} className="mb-3">
                        <Col sm={3}>
                            <Form.Label     column sm={2}  size="lg">name</Form.Label>
                        </Col>
                        <Col>
                            <Form.Control   type="text" placeholder="demo" size="lg" className="input" id="name"
                                            ref={inputName} disabled={disabled}
                                            onChange={onTextChanged} onKeyPress={onKeyPress} onFocus={onFocus}/>
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} className="mb-3">
                        <Col sm={3}>
                            <Form.Label     column sm={2}>password</Form.Label>
                        </Col>
                        <Col>
                            <Form.Control   type="password" size="lg" className="input" id="password" autoComplete="off"
                                            ref={inputPswd}  disabled={disabled}
                                            onChange={onTextChanged} onKeyPress={onKeyPress} onFocus={onFocus}/>
                        </Col>
                    </Form.Group>
                </Form>
            </Modal.Body>
			<Modal.Footer   className="user-auth">
                {(refState.current === cLoginConnecting || refState.current === cLoginSuccessAfter)
                    ?   <table  style={{width: "100%"}}>
                            <tbody>
                            <tr style={{height: "50px"}}>
                                <td style={{width: "90%"}}>
                                    <i>{message}</i>
                                </td>
                                <td>
                                    <Spinner animation="border" variant="primary" size="lg"/>
                                </td>
                            </tr>
                            </tbody>
                        </table>	
                    :	<table  style={{width: "100%"}}>
                            <tbody>
                            <tr style={{height: "50px"}}>
                                <td style={{width: "10%"}}>
                                    <ButtonAbout    ref={refButtonAbout} onClick={()=>about()}/>
                                </td>
                                <td style={{width: "80%"}}>
                                    <i> for demo purposes please use name/password "demo/demo" </i>
                                </td>
                                <td>
                                    <Button ref={btnLogin} variant="primary" disabled={disabled} onClick={login} type="submit">
                                        Login
                                    </Button>            
                                </td>
                            </tr>
                            </tbody>
                        </table>
				}
            </Modal.Footer>
        </Modal>
    )
}

export default ModalUserAuth;
//export default React.memo(ModalUserAuth);
