import React from 'react';
import ProgressBar from 'react-bootstrap/ProgressBar'

export function ProgressTimeout(props) {
    return (
        <div style={{width: "100%", margins: "1em 0 1em 0"}}>
            <ProgressBar variant="info" animated label={(props.now/1000).toFixed(1) + " s"} max={props.timeout} now={props.now}/>
        </div>
    )
}