import React, { Component } from "react";
import Dropdown from "react-bootstrap/Dropdown";
import DropdownButton from 'react-bootstrap/DropdownButton';
import {cfg, monthsDays} from "./Cfg.js";

class DropDownMonth extends Component {
    constructor(props) {
		super(props);
		this.onSelect = this.onSelect.bind(this);
	}
	render() { // onMetersDataChanged, disabled
		let caption = "" + monthsDays[cfg.monthIdx];
		let onItemClick = this.onSelect;
		return (
			<DropdownButton title={caption} variant="secondary" menuVariant="dark" 
							className="d-grid gap-2"
							disabled={this.props.disabled} >
				{monthsDays.map(function(month, idx) {
					var active = (month === monthsDays[cfg.monthIdx]);
					return (
						<Dropdown.Item key={month} active={active} onClick={onItemClick}>
							{month}  
						</Dropdown.Item>
					)
				})}
    		</DropdownButton>
		)
	}
/*
			<Dropdown onSelect={this.onSelect} disabled={this.props.disabled} drop="down" className="d-grid gap-2">
				<Dropdown.Toggle variant="secondary">
					{caption}
				</Dropdown.Toggle>
				<Dropdown.Menu variant="dark">
					{monthsDays.map(function(size) {
						var active = (size === monthsDays[cfg.monthIdx]);
						return (
							<Dropdown.Item key={size} eventKey={size} active={active}>{size}</Dropdown.Item>
						)
					})}
				</Dropdown.Menu>
			</Dropdown>
*/
	onSelect(e) {
		let item = e.target.text;
		if (item !== monthsDays[cfg.monthIdx]) {
			let newIdx = monthsDays.indexOf(item);
			if (newIdx < 0)
				alert("Error in component DropDownMonth: error selected value " + e)
			else {
				cfg.monthIdx = newIdx;
				this.forceUpdate();
				if (this.props.onChanged === undefined)
					alert("Error in component DropDownMonth: undefined callback function onChanged")
				else				
					this.props.onChanged(item);
			};
		};
	}
}

export default React.memo(DropDownMonth)