import {toast} from 'react-toastify';
import customToast from './customToast.js';

export const utils = {
	//optionsDateTime: {year: 'numeric', month: '2-digit', day: '2-digit', second: '2-digit', hour: '2-digit', minute: '2-digit', hour12: false},
	formatDateTime: function(dt) {
		//return Intl.DateTimeFormat('en-GB', this.optionsDateTime).format(dt).replace(/\//g,'-').replace(',','');
		let _dt = new Date(dt);
		return _dt.toLocaleTimeString() + " " + _dt.toLocaleDateString();
	},
	formatDate: function(dt) {
		//return Intl.DateTimeFormat('en-GB', this.optionsDateTime).format(dt).replace(/\//g,'-').replace(',','');
		let _dt = new Date(dt);
		return _dt.toLocaleDateString();
	},
	sleep: function(ms) {
		return new Promise(resolve => {
				setTimeout(() => resolve(), ms)
			}
		)
	},
	toastAbout: function(aMsg, aOptions) {
		var options = {containerId: 'about', position: "bottom-center", autoClose: false};
		if ( this.isLiteralObject(aOptions) ) {
			options = {...options, ...aOptions};
		}
		return customToast.about(aMsg, options);
	},
	toastInfo: function(aMsg, aPosition = "bottom-center") {
		return customToast.info(aMsg, {containerId: 'info', position: aPosition, autoClose: false});
	},
	toastSuccess: function(aMsg, aPosition = "bottom-center") {
		return customToast.success(aMsg, {containerId: 'info', position: aPosition, autoClose: 5000});
	},
	toastError: function(aMsg, aOptions) {
		var options = {containerId: 'info', position: "bottom-center", autoClose: false};
		if ( this.isLiteralObject(aOptions) ) {
			options = {...options, ...aOptions};
		}
		return customToast.error(aMsg, options);
	},
	toastsClear: function() {
		toast.dismiss();
	},
	pxToEm: function(px, element) {
		element = element === null || element === undefined ? document.documentElement : element;
		var temporaryElement = document.createElement('div');
		temporaryElement.style.setProperty('position', 'absolute', 'important');
		temporaryElement.style.setProperty('visibility', 'hidden', 'important');
		temporaryElement.style.setProperty('font-size', '1em', 'important');
		element.appendChild(temporaryElement);
		var baseFontSize = parseFloat(getComputedStyle(temporaryElement).fontSize);
		temporaryElement.parentNode.removeChild(temporaryElement);
		return px / baseFontSize;
	},
	isLiteralObject: function(a) {
		return (!!a) && (a.constructor === Object);
	},
	genRandomDate(dateFrom, deltaYears, dateYears, random1, random2 = 1) {
		const year = 31536000000;
		return  dateFrom + deltaYears*year + dateYears * Math.abs( Math.sin(random1 * random2) ) * year;
	},
	genRandonInteger(min, max, random) {
		return Math.round( min + ((1 + Math.sin(random)) * (max - min) / 2) );
	}
}