import React from "react";
import { Button, Modal, Table } from "react-bootstrap";
import { cfg } from "./Cfg";

export const ModalUserInfo = React.forwardRef( (props, ref) => {
//export function ModalUserInfo() {
    const [show, setShow] = React.useState(false);

    React.useImperativeHandle(ref, () => (
            { showUserInfo: (visible = true) => setShow(visible) } 
        )
    );

    function tableCell(aData, aKey, aStyle = {}) {
        let style = {...aStyle};
        if ( typeof aData === "object" ) {
            let text = aData[aKey] ? aData[aKey] : null;
            if (!text && Object.keys(aData).length > 0) {
                text = aData[Object.keys(aData)[0]]
            };
            if (!text) {
                return null;
            }
            let title = aData.hint;
            let comp = aData.component;
            let disabled = aData.disabled ? aData.disabled : false;
            switch (comp) {
                case "button": return (
                    <span title={title} style={style}>
                        <Button disabled={disabled}> 
                            {text} 
                        </Button>
                    </span>
                );
                default: return (
                    <span title={title} style={style}> {text} </span>
                );
            }
        } else if ( typeof aData === "string" ) {
            return (
                <span style={style}> {aData} </span>
            )
        } else {
            return null;
        }
    }

    if (show) {
        return (
            <Modal 	show={show} onHide={ () => setShow(false) }
                    size="sm" aria-labelledby="contained-modal-title-vcenter" 
                    centered scrollable autoFocus
                    dialogClassName="modal-70w" fullscreen="sm-down" >
                <Modal.Header className="user-auth" closeButton>
                    <Modal.Title id="contained-modal-title-vcenter">
                        User's Info
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Table size="sm" tabIndex="0" >
                        <tbody>
                            {cfg.user.map((item, index) => {
                                let key = Object.keys(item)[0];
                                return (
                                    <tr key={index} style={{verticalAlign: "middle"}}>
                                        <td style={{textAlign: "start"}}> {tableCell(key)} </td>
                                        <td style={{textAlign: "end"}}> {tableCell(item, key)} </td>
                                    </tr>
                                )
                            })}
                        </tbody>
                    </Table>
                </Modal.Body>
            </Modal>
        );
    } else {
        return null
    }
})