import React from 'react';
import ProgressBar from 'react-bootstrap/ProgressBar';
import { toast } from 'react-toastify';

const UserInactivityStatus = React.forwardRef( (props, ref) => {
        const [timeout, setTimeout] = React.useState( {now: Infinity, max: 0} );
    
        React.useImperativeHandle(ref, () => ({
                setProgress: (value) => setTimeout(value),
            })
        );

        if ( isFinite(timeout.now) ) {
            if (timeout.now > 0)
                return(
                    <span className="userInactivityStatus"> 
                        user's inactivity timeout: {timeout.now} s
                    </span>
                )
            else {
                return null;
            }
        } else
            return null;
    }
)

const UserInactivityProgress = React.forwardRef( (props, ref) => {
        const [timeout, setTimeout] = React.useState( {now: Infinity, max: 0} );
        
        React.useImperativeHandle(ref, () => ({
                setProgress: (value) => setTimeout(value),
            })
        );
        
        let now = isFinite(timeout.now) ? timeout.now : 0;
        let max = now ? timeout.max : 100;
        let visibility = (now <= 0) ? {visibility: "hidden"} : {visibility: "visible"};
        return(
            <ProgressBar    className="userInactivityProgress" 
                            max={max} now={now} style={visibility} 
            />
        ) 
    }
)

const  UserInactivityToast = React.forwardRef( (props, ref) => {
        const toastId = React.useRef(null);
        const progress = React.useRef(null);
        function dismiss() {
            if (toastId.current) {
                toast.dismiss(toastId.current);
                //toastId.current = null;
            }
        };
        
        function show(timeout) {
            progress.current = timeout.now / timeout.max;
            let msg = timeout.now > 0 ? `...    ${timeout.now}s left` : "";
            msg = "User's inactivity timeout" + msg;
            let options = { position: "top-center", type: toast.TYPE.WARNING, hideProgressBar: false,
                            progress: progress.current, closeButton: false, 
                            className: 'toast-info-container', containerId: 'info',
                            onClose: () => toastId.current = null };
            if (timeout.now <= 0) {
                options = {...options, autoClose: false, type: toast.TYPE.ERROR, closeButton: true};
            }
            if (toastId.current && toast.isActive(toastId.current)) {
                    toast.update(toastId.current, { ...options, render: msg });
                } else {
                    toast.dismiss();
                    toastId.current = toast(msg, options);
                }
        }
        React.useImperativeHandle(ref, () => (
                {   setProgress: (timeout) => {
                        if ( isFinite(timeout.now) ) {
                                show(timeout);
                            } else {
                                dismiss();
                            }
                        } 
                }

            )
        );
        
        return(null);
    }
)

export {UserInactivityStatus, UserInactivityProgress, UserInactivityToast};