export function MarkerCounters(props) {
	return (
		<>
		<CounterTL value={props.values[tl]} />
		<CounterBL value={props.values[bl]} />
		<CounterTR value={props.values[tr]} />
		<CounterBR value={props.values[br]} />
		<CounterCL value={props.values[cl]} />
		<CounterCR value={props.values[cr]} />
		<CounterBC value={props.values[bc]} />
		<CounterTC value={props.values[tc]} />
		</>
	)
}
function MapPlaceholder() {
  return (
    <p>
		Map of Objects.{' '}
		<noscript>You need to enable JavaScript to see this map.</noscript>
    </p>
  )
}
const refMarkers = {current: []}; // pseudo hook effect
export function showTooltip(idx) {
	if (idx >=0 && idx < refMarkers.current.length) {
		if (refMarkers.current[idx].current) {
			if (!refMarkers.current[idx].current.isTooltipOpen()) 
				refMarkers.current[idx].current.toggleTooltip();
		} 
	}
}
export function hideTooltip(idx) {
	if (idx >=0 && idx < refMarkers.current.length)
		if (refMarkers.current[idx].current) {
			if (refMarkers.current[idx].current.isTooltipOpen())
				refMarkers.current[idx].current.toggleTooltip();
	} 
}

function LlMap(props) {
	const LeafIcon = L.Icon.extend({
		options: {}
	});
	const blueIcon = new LeafIcon({
	    iconUrl: process.env.PUBLIC_URL + '/img/marker-blue.png',//"https://github.com/pointhi/leaflet-color-markers/blob/master/img/marker-icon-blue.png?raw=true"
		shadowUrl: process.env.PUBLIC_URL + '/img/marker-shadow.png',
		iconAnchor: [12, 40]
    });
    const grayIcon = new LeafIcon({
	    iconUrl: process.env.PUBLIC_URL + '/img/marker-yellow.png',
		shadowUrl: process.env.PUBLIC_URL + '/img/marker-shadow.png',
		iconAnchor: [12, 40]
    });
	//const [icon, setIcon] = useState(grayIcon);
	function clickMarker(object) {
		selectMarker(object);
	};
	
	const [state, setState] = React.useState({});
	function mapEvent() {
		if (mapWnd.countsChanged)
			setState( {state} );
	};
	
	const refMap = React.useRef(); // not used - just for trick
	//const refMarkers = React.useRef([]);
	if (refMarkers.current.length !== data.objects.length) {
        refMarkers.current = data.objects.map((_, i) => refMarkers.current[i] || React.createRef());
    }
	function focusMarker(icon) {
		let marker = refMarkers.current.find( (mrk) => 
			{return mrk.current._icon === icon.currentTarget} );
		if (marker)
			if (!marker.current.isTooltipOpen())
				marker.current.toggleTooltip();
	};
	function unfocusMarker(icon) {
		let marker = refMarkers.current.find( (mrk) => 
			{return mrk.current._icon === icon.currentTarget} );
		if (marker && refMarkers.current.indexOf(marker) !== data.objects.indexOf(cfg.currentObject))
			if (marker.current.isTooltipOpen())
				marker.current.toggleTooltip();
	};

	const nObjects = React.useRef(0);
	React.useEffect(	() => {
		if (nObjects.current !== data.objects.length) {
			//refMap.current.target._events.moveend[3].fn();  
			nObjects.current = data.objects.length;
			setState( {state} );
		};
		refMarkers.current.forEach( 
			(refMarker) => {
				if (refMarker.current) {
					refMarker.current._icon.addEventListener("focus", focusMarker); 
					refMarker.current._icon.addEventListener("blur", unfocusMarker); 
				};
			} 
		);
		return function unmount() {
			refMarkers.current.forEach( 
				(refMarker) => {
					if (refMarker.current) {
						if (refMarker.current._icon) {
							refMarker.current._icon.removeEventListener("focus", focusMarker); 
							refMarker.current._icon.removeEventListener("blur", unfocusMarker); 
						}
					}
				} 
			)
		}
	} );
	function keypressMarker(e, object) {
		if (e.originalEvent.code === "Space" || e.originalEvent.code === "Enter") 
			selectMarker(object);
	};
	function selectMarker(object) {
		if (cfg.currentObject !== object) {
			cfg.currentObject = object;
			if (props.onChanged)
				props.onChanged(object)
			else
				alert("Error in component LlMap: undefined callback function onChanged");
		}
	};
	
	let mapCfg = cfg.mapLoad();
	return (
		<>
		<MapContainer 	center={[mapCfg.lat, mapCfg.lng]} zoom={mapCfg.zoom} scrollWheelZoom={false}
						style={{width: "100%", height: "100%"}} 
						whenReady={mapInstance => { refMap.current = mapInstance } } placeholder={<MapPlaceholder/>}
						>
			<TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />
			{data.objects.map( function(object, index) {
				let lon = object.coord.lon;
				let lat = object.coord.lat;
				let cur = object === cfg.currentObject;
				let dir = cur ? "bottom" : "top";
				let per = cur ? true : false;
				let ofs = cur ? [0, 0] : [0, -40];
				let opy = cur ? 0.7 : 1.0;
				let icon = cur ? blueIcon : grayIcon;
				let zdx = cur ? 1000 : 0;
				//var refMarker = React.createRef();
				//refMarkers.push(refMarker);
				if (props.tooltips) {
					return(
						<Marker position={[lat, lon]} key={index} icon={icon} zIndexOffset={zdx} ref={refMarkers.current[index]}
								eventHandlers={{click: (e) => clickMarker(object), 
												keypress: (e) => keypressMarker(e, object)}} 
								autoPanOnFocus>
							{cur && 
								<Tooltip direction={dir} offset={ofs} opacity={opy} permanent={per}>
									{object.name}
							</Tooltip>}
							{!cur && 
								<Tooltip direction={dir} offset={ofs} opacity={opy} permanent={per}>
									{object.name} <br/> {object.address} <br/> meters: {object.metersCount}
							</Tooltip>}
						</Marker>
				)} else {
					return(
						<Marker position={[lat, lon]} key={index} icon={icon}/>
				)};	
			})}
			{data.objects.length > 0 &&
				<MapBounds/>
			}
			<MapEvents onMapEvent={mapEvent}/>
			<CenterMarker tooltips={props.tooltips}/>
		</MapContainer>
		<MarkerCounters values={mapWnd.counts}/>
		</>
	); //
}

export default React.memo(LlMap)
