import React from "react";
import Offcanvas from "react-bootstrap/Offcanvas";
import ListGroupToggleViewMapOrMeters from "./ListGroupToggleViewMapOrMeters.js";
import {cfg} from './Cfg.js';
import ButtonUser from "./ButtonUser.js";

const SlidePanelRight = React.forwardRef( (props, ref) => {
		const [show, setShow] = React.useState(false);

		React.useImperativeHandle(ref, () => (
				{ open: (value) => setShow(true) } 
			)
		);

		function onClose(e) {
			setShow(false);
		}

		return (
			<Offcanvas id="slide-panel-right" placement="end" show={show} onHide={onClose}>
				<Offcanvas.Header closeButton>
					<Offcanvas.Title>
						Main Menu
					</Offcanvas.Title>
				</Offcanvas.Header>
				<Offcanvas.Body	style={{padding: "0"}}>
					{show &&
						<>
						<ListGroupToggleViewMapOrMeters onChanged={props.onSelectTab} currObj={cfg.currentObject} 
														disabled={props.disabled}/>
						<div  	style={{margin: "1em", marginLeft: "auto", marginRight: "auto", width: "fit-content"}}>
							<ButtonUser	caption="User's Info"	title=""	variant="primary" 
										disabled={props.disabled} 
										onClick={props.showUserInfo}
							/>
						</div>
						</>
					}
				</Offcanvas.Body>
			</Offcanvas>
		)
	} 
)

export default React.memo( SlidePanelRight );
//export default SlidePanelRight;


