import React from 'react';
import Modal from "react-bootstrap/Modal";
import Spinner from "react-bootstrap/Spinner";
import Button from "react-bootstrap/Button";
import { Request } from "./Request.js";
import { ProgressTimeout } from "./ProgressTimeout.js";

export function NetOperation(props) { // params = {procedure, onFinish, setStatus, dataRef}, statusMessage
	const [working, setWorking] = React.useState(false);
	//const [error, setError] = React.useState([]);
	const [loading, setLoading] = React.useState(true);
	const message = React.useRef("");
	const [progress, setProgress] = React.useState(0);
	const refBtnRetry = React.useRef();
	const cTimeout = 6000;
	const errors = React.useRef(null);

	function onFinished(result) {
		errors.current = null;
		props.params.onFinish(result);
		setWorking(false);
		if (props.params.setStatus)
			props.params.setStatus("");
	}
	function onError() {
		setLoading(false);
		if (props.params.onError) {
			props.params.onError();
		};
		if (props.params.setStatus) {
			props.params.setStatus("");
		};
	}
	function onRequest(result) {
		errors.current = null;
		errors.status = undefined;
		if (result.status === 200)
			onFinished(result)
		else {
			errors.current = [result.message];////setError(error[0].toUpperCase() + "<p>" + error.slice(1) + "</p>");
			if (result.errors)
				errors.current = errors.current.concat(result.errors);
			errors.status = result.status;
			onError();
		}
	}
	function onProgress(value) {
		setProgress(value);
	}
	function startWorking() {
		if (loading) {
			new Request(cTimeout / 2, onRequest, {interval: cTimeout/10, procedure: onProgress}, props.params.procedure /*null*/, props.params.dataRef) // !!
		} else { 
			setLoading (true, new Request(cTimeout, onRequest, {interval: cTimeout/10, procedure: onProgress}, props.params.procedure, props.params.dataRef) );
			props.params.setStatus(message.current);
		};
		setWorking(true);
	};
	React.useEffect(	() => 	{
		if (refBtnRetry.current) {
			refBtnRetry.current.focus();
		}
	}); 

	if (!working && props.params.procedure) {
		message.current = props.statusMessage;
		startWorking();
	};
	if (errors.current) {
		let mark = errors.status !== "" ? "" : 'This error was emulated for demo-purpose ! Please click/press button "Retry".';
		return (
			<Modal 	show={true} size="lg" centered  aria-labelledby="contained-modal-title-vcenter"
								dialogClassName="modal-50w" fullscreen="lg-down">
				<Modal.Header>
					<Modal.Title id="contained-modal-title-vcenter">
						Network Operation Error
					</Modal.Title>
				</Modal.Header>
				<Modal.Body>
					{errors.current.map( (error, index) => {
						return(
							<p style={{color: "red"}} key={index}><b> {error} </b></p>
						)})}		
					<p><mark> {mark} </mark></p>
				</Modal.Body>
				<Modal.Footer style={{height: "4em", justifyContent: "space-between"}}>
					{loading &&
						<div style={{display: "flex", alignItems: "center"}}>
							<Spinner animation="border" variant="primary" size="lg"/>
							<div style={{marginLeft: "1em"}}> <i> connecting server... </i> </div>
						</div>
					}
					{loading && cTimeout >= 1000 &&
						<div style={{width: "40%", display: "flex", alignItems: "center"}}>
							<div style={{marginRight: "1em"}}> <i> waiting </i> </div>
							<ProgressTimeout timeout={cTimeout} now={progress} />
						</div>			
					}
					{!loading &&
						<>
						<span/>
						<Button variant="primary" size="sm" title="repeat operation" aria-label="repeat" ref={refBtnRetry}
								style={{fontSize: "1.0rem", fontWeight: "500"}} onClick={ ()=>startWorking() } >
							<img src={process.env.PUBLIC_URL + 'img/synchronize.svg'} alt="repeat operation" style={{marginRight: "0.3em"}}/>
							Retry
						</Button>
						</>
					}
				</Modal.Footer>
			</Modal>
		)
	} else {
		return null;
	};
}
