import {cfg} from "./Cfg.js";
import {utils} from "./Utils.js";

const METERS = [
	{model: 'NiK 2104', link: 'https://nik.net.ua/en/product/nik-2104-new-1', digits: '6.2'}, 
	{model: 'NiK 2100', link: 'https://nik.net.ua/en/product/nik-2100-new__trashed', digits: '6.2'},  
	{model: 'NiK 2108', link: 'https://nik.net.ua/en/product/nik-2108', digits: '6.2'},  
	{model: 'NiK 2106', link: 'https://nik.net.ua/en/product/nik-2106-new-1', digits: '6.2'}
];

export default class Meter {
	constructor(idx) {
		this.id = idx;
		this.title = cfg.prefixMeterIdx.short + String(this.id+1);
		this.energy = [null, null, null, null];
	}
	setMeterData() {
		this.tarifCount = this.getTarifCount(this.id, cfg.currentObj.id);
		if (!this.energy) {
			this.energy = [null, null, null, null];
			this.title = cfg.prefixMeterIdx.short + String(this.id+1);
		}
		this.energy[0] = Math.round(cfg.monthsDays[cfg.monthIdx] * 100 * (4 + 2 * Math.sin(5*this.id + 14*cfg.currentObj.id + 10*cfg.monthIdx))) / 100;
		if (this.tarifCount === 1)
			this.energy[1] = this.energy[0];
		else if (this.tarifCount === 2) {
			this.energy[1] = Math.floor(this.energy[0] * (0.7 + 0.2*Math.sin(15*this.id + 4*cfg.currentObj.id)) * 100) / 100;
			this.energy[2] = Math.round((this.energy[0] - this.energy[1]) * 100) / 100;
		} else {
			this.energy[1] = Math.floor(this.energy[0] * (0.6 + 0.15*Math.sin(15*this.id + 4*cfg.currentObj.id)) * 100) / 100;
			this.energy[2] = Math.floor((this.energy[0] - this.energy[1])  * (0.25 + 0.1*Math.sin(15*this.id + 4*cfg.currentObj.id)) * 100) / 100;
			this.energy[3] = Math.round((this.energy[0] - this.energy[1] - this.energy[2]) * 100) / 100;
		};
	}
	setMeterInfo() {
		this.info = [];
		const MS_PER_DAY = 1000 * 60 * 60 * 24;
	
		let dt0 = (new Date(new Date().getFullYear(), 0, 1)).valueOf();
		let modelNo = utils.genRandonInteger(0, METERS.length - 1, this.id*20 + 5);
		this.info.push( {item: "meter", value: METERS[modelNo].model, link: METERS[modelNo].link } );
		let serialNo = (utils.genRandonInteger(10000, 999999, this.id*9 + 4)).toString().padStart(8, '0');
		this.info.push( {item: "serial no.", value: serialNo} );
		this.info.push( {item: "number of tariffs", value: this.tarifCount} );
		this.info.push( {item: "digits on LCD", value: METERS[modelNo].digits} );
		let dt = utils.genRandomDate(Date.now(), 0, 1, 2*this.id) + 1;
		if (Math.ceil((dt - Date.now()) / MS_PER_DAY) < 7) {
			this.info.push( {item: "next verification date", value: utils.formatDate(dt), note: "alarm"} )
		} else 	if (Math.ceil((dt - Date.now()) / MS_PER_DAY) < 30) {
			this.info.push( {item: "next verification date", value: utils.formatDate(dt), note: "warning"} )
		} else {
			this.info.push( {item: "next verification date", value: utils.formatDate(dt)} )
		};		
		dt = utils.genRandomDate(dt0, -8, 2, this.id+1);
		this.info.push( {item: "date of manufacture", value: utils.formatDate(dt)} );
		dt = utils.genRandomDate(dt, 0, 1, 2*this.id + 1);
		this.info.push( {item: "date of installation", value: utils.formatDate(dt)} );
		this.info.push( {item: "seal no.", value: utils.genRandonInteger(100, 999, this.id+4)} );
		this.info.push( {item: "RS-485 channel no.", value: utils.genRandonInteger(1, cfg.currentObj.rs485Count, this.id*4+4)} );
		this.info.push( {item: "RS-485 address", value: Number(serialNo.toString().substring(6))} );
	}
	getTarifCount(i, j) {
		let k = utils.genRandonInteger(0, 10, 20*i + 50*j);
		return (k >= 5)
			? 3
			: (k >= 2 ) ? 2 : 1;
		/*
		let k = Math.sin(20*i + 50*j);
		if (k > 0)
			return 3
		else if (k > -0.5)
			return 2
		else	
			return 1;
		*/
  };
}
