import React, { Component } from "react";
import Dropdown from "react-bootstrap/Dropdown";
import DropdownButton from 'react-bootstrap/DropdownButton';
import {cfg, sortKinds} from "./Cfg.js";
//const uuidv4 = require('uuid');

class DropDownSort extends Component {
    constructor(props) { // onChanged, disabled
		super(props);
		this.onSelect = this.onSelect.bind(this);
	}
	render() {
		let caption = "Sort: " + sortKinds[cfg.sortKindIdx];
		let onItemClick = this.onSelect;
		return (
			<DropdownButton title={caption} variant="secondary" menuVariant="dark" 
							className="d-grid gap-2"
							disabled={this.props.disabled} >
				{sortKinds.map(function(sort) {
					var active = (sort === sortKinds[cfg.sortKindIdx]);
					return (
						<Dropdown.Item key={sort} eventKey={sort} active={active} onClick={onItemClick}>
							{sort}  
						</Dropdown.Item>
					)
				})}
    		</DropdownButton>
		)
	}
/*
			<Dropdown onSelect={this.onSelect} disabled={this.props.disabled} drop="down" className="d-grid gap-2">
				<Dropdown.Toggle variant="secondary">
					{caption}
				</Dropdown.Toggle>
				<Dropdown.Menu variant="dark">
					{sortKinds.map(function(sort) {
						var active = (sort === sortKinds[cfg.sortKindIdx]);
						return (
							<Dropdown.Item key={sort} eventKey={sort} active={active}>
								{sort}
							</Dropdown.Item>
						)
					})}
				</Dropdown.Menu>
			</Dropdown>
*/
	onSelect(e) {
		let item = e.target.text;
		if (item !== sortKinds[cfg.sortKindIdx]) {
			let newIdx = sortKinds.indexOf(item);
			if (newIdx < 0)
				alert("Error in component DropDownSort: error selected value " + e)
			else {
				cfg.sortKindIdx = newIdx;
				this.forceUpdate();
				if (this.props.onChanged === undefined)
					alert("Error in component DropDownSort: undefined callback function onChanged")
				else				
					this.props.onChanged(item);
			};
		};
	}
}

export default React.memo(DropDownSort);