import React from 'react';
import Table from "react-bootstrap/Table";
import './TableInfo.css';
import {cfg, tileSizesCla} from './Cfg.js';

function TableInfo(props) {
	var fontSize = parseFloat(getComputedStyle(document.querySelector(':root')).getPropertyValue('font-size'));
	var lineHeight = parseFloat(getComputedStyle(document.querySelector(':root')).getPropertyValue('line-height'));
	let sel = document.querySelector('.main-cards');
	if (sel) {
		let cardWdt = parseFloat(getComputedStyle(sel).getPropertyValue('--width-' + tileSizesCla[Math.min(1, cfg.sizeTileIdx)]));
		let cardCfc = parseFloat(getComputedStyle(sel).getPropertyValue('--font-size-card-coef'));
		fontSize = 0.8 * cardWdt / cardCfc;
		lineHeight = cardWdt / cardCfc / 26;
	} else {
		console.error(`style ".main-card" not found`);
	}

	return(
		<div className="table-info" style={{lineHeight}}>
		<Table size="sm">
			<tbody>
				{props.source.map( (entry, index) => {
						if (entry.item) {
							let itemClass = "table-info-text";
							switch (entry.note) {
								case "warning":
									itemClass = "table-info-warning";
									break;
								case "alarm":
									itemClass = "table-info-alarm";
									break;
								default:
									break;
							};
							return (
								<tr key={index}>
									<td style={{textAlign: "left", fontSize}} className={itemClass}> {entry.item} </td>
									<td style={{textAlign: "right", fontSize}} className={itemClass}> 
										{ entry.link
											?	<a href={entry.link} target="_blank" rel="noopener noreferrer"> {entry.value} </a>
											:	entry.value
										}
									</td>
								</tr>
							)
						} else {
							return (
								<tr key={index}>
									<td colSpan={2} style={{textAlign: "center"}}> 
										<b><i> {entry.value} </i></b> 
									</td>
								</tr>
							)
						}
					})
				}
			</tbody>
		</Table>
		</div>
	);
}
/*
<Table size="sm">
	<tbody>
		{props.source.map( (entry, index) => {
			return(
				<tr key={index}>
					<td style={{textAlign: "left"}}>{entry.item}</td>
					<td style={{textAlign: "right"}}>{entry.value}</td>
				</tr>
			)})}
	</tbody>
</Table>
*/					
export default TableInfo;