import React, { Component } from "react";
import Form from "react-bootstrap/Form";
import FormControl from "react-bootstrap/FormControl";
import {cfg} from "./Cfg.js";

class EditFilter extends Component {
    constructor(props) {
		super(props);
		this.onChange = this.onChange.bind(this);
	}
	render() {
		var value = cfg.filter;// this.props.value;
		return (
			<Form >
				<FormControl
					type="search"
					value={value}
					placeholder={"filter: " + cfg.prefixMeterIdx.short + "#"}
					className="me-2"
					onChange={this.onChange}
					disabled={this.props.disabled}
				/>
			</Form>
		);
	}
	onChange(e) {
		this.forceUpdate();
		if (this.props.onChanged === undefined)
			alert("Error in component EditFilter: undefined callback function onChanged")
		else	
			this.props.onChanged(e.currentTarget.value);
	}
}

export default EditFilter;