import {utils} from './Utils.js';

const Kyiv_Obolon = {lon: 30.515, lat: 50.522};
//const City = {lon: -0.09, lat: 51.505};

export class Obj {
	constructor(idx, onLast) {
		this.id = idx;
		this.name = `Object #${idx}`;
		//this.address = "";
		//this.post_address = "";
		let sin = Math.sin(idx), cos = Math.cos(idx);
		//this.metersCount = 20 + Math.trunc(10 * sin);
		let metersCount = 20 + Math.trunc(10 * sin);
		const pos = Kyiv_Obolon, d = 0.005;
		let r = d * Math.sin(4 * idx);
		let x = pos.lon + r * cos; 
		let y = pos.lat + r * sin;
		this.coord = {lon: x, lat: y};
		this.address = fetchAddress(this, onLast, metersCount);
	}
}
