export function fetchAddress(object) {
	const headers = { 'Content-Type': 'application/json' };
	let uri;
	if (object.id % 3 === -1 ) {// !!
		uri = "https://nominatim.openstreetmapsss.org/reverse?accept-language=en&format=json"
	} else {
		uri = "https://nominatim.openstreetmaps.org/reverse?accept-language=en&format=json"
	}
    return fetch(`${uri}&lat=${object.coord.lat}&lon=${object.coord.lon}`, headers)
		.then( (response) => {
				if (!response.ok) {
					throw new Error(`HTTP error! Status: ${ response.status }`);
				}
				return response.json()
			}
		)
		.then( (result) => {return ( {error: 0, response: result} )} )
		.catch( (error) => {return ( {error: -1, response: error} )} );
}
