export function newObject(idx) {
	let sin = Math.sin(idx), cos = Math.cos(idx);
	const pos = Kyiv_Obolon, d = 0.005;
	let r = d * Math.sin(4 * idx);
	let x = pos.lon + r * cos;
	let y; 
	if (idx === -4) // !!
		y = "lattitude"
	else 
		y = pos.lat + r * sin;
	let metersCount = utils.genRandonInteger(10, 30, idx*10 + 10);
	let rs485Count = utils.genRandonInteger(1, 4, idx*10 + 10);
	return {id: idx, name: `Object #${idx}`, coord: {lon: x, lat: y}, metersCount: metersCount, rs485Count: rs485Count};
}
