export class Request {
	timeout = null;      // <= setTimeout, used as flag for longing timeout
    interval = null;     // <= setInterval, used for indicating timeout progress
    onFinish = null;     // f()
	start = null;        // now()
	result = null;       // object {status: N, message: String} used as a reference !!
    progress = null;     // {interval: NN ms, procedure: f()}
    progressValue = 0;   // 

    constructor (timeout, onFinish, progress, getData, dataRef) {
        if (onFinish)
            this.onFinish = onFinish
        else {
            this.result = {status: 400, message: `error in Request.constructor: undefined function onFinish`};
            return;
        };
        this.result = {status: 0, message: `data querying...`};
        let waitFor = timeout ? timeout : 0;//3000;
        this.start = Date.now();
        if (waitFor) {
            this.timeout = setTimeout( this.doTimeout.bind(this), waitFor );
            if (progress && progress.procedure && progress.interval && progress.interval < waitFor / 2) {
                this.progress = progress;
                this.progressValue = 0;
                this.interval = setInterval( this.doProgress.bind(this), progress.interval );
                this.doProgress();
            }
        } else {
            this.timeout = -1;
        }
        if (getData) {
            getData( {ptr: dataRef, onFinish: this.doSuccess.bind(this), result: this.result} );
        }
    }
    doTimeout(onFinish) {
        if (this.timeout) {
            this.doProgress() ;
            this.result.status = "";
            this.result.message = `Server didn't answer in ${((Date.now() - this.start)/1000).toFixed(1)} s`;
            this.reset();
            this.onFinish(this.result);
        }
    }
    doProgress() {
        if (this.timeout && this.interval && this.progress) {
            this.progress.procedure(this.progressValue);
            this.progressValue += this.progress.interval;
        }
    }
	reset() {
		if (this.interval) {
			clearInterval(this.interval);
			this.interval = null;
		};
		if (this.timeout) {
			clearTimeout(this.timeout);
			this.timeout = null;
		};
        this.start = null;
        this.progress = null;
        this.progressValue = 0;
	}
	doSuccess(message) {
		if (this.timeout) {
			//this.result.status = 200;
			//this.result.message = `answer received in ${Date.now() - this.start} ms`;
			this.reset();
            if (message !== undefined && message != null)
                this.result.message = message;
			this.onFinish(this.result);
		};
	}
}
