import './ModalObjectInfo.css';
import './TableInfo.css';
import React from 'react';
import Modal from "react-bootstrap/Modal";
import Spinner from "react-bootstrap/Spinner";
import Table from "react-bootstrap/Table";
import Placeholder from "react-bootstrap/Placeholder";
import Button from "react-bootstrap/Button";
import {data} from "./Data.js";
import {utils} from "./Utils.js";

function CellPlaceHolder(props) {
	return (
		<Placeholder animation="glow">
			<Placeholder xs={props.size} /> 
		</Placeholder>
	)
}

function TablePlaceHolder(props) {
	const rows = [];
	for (let i=0; i < props.rows; i++) 
		rows.push({left: 9 + Math.trunc(3*Math.sin(i)), right: 9 + Math.trunc(3*Math.cos(i))});
	return (
		<>
		{rows.map( (row, idx) => {
			return(
				<tr key={idx}>
					<td style={{textAlign: "left"}}>
						<CellPlaceHolder size={row.left} />
					</td>
					<td style={{textAlign: "right"}}>
						<CellPlaceHolder size={row.right} />
					</td>
				</tr>
			)
		}
		)}
		</>
	)
}

function TableInfo(props) {
	return (
		<>
		{data.objectInfo.items.map( (entry, index) => {
			if (entry.item) {
				let itemClass = "table-info-text";
				switch (entry.note) {
					case "warning":
						itemClass = "table-info-warning";
						break;
					case "alarm":
						itemClass = "table-info-alarm";
						break;
					default:
						break;
				};
				return (
					<tr key={index}>
						<td style={{textAlign: "left"}} className={itemClass}> {entry.item} </td>
						<td style={{textAlign: "right"}} className={itemClass}> 
							{ entry.link
								?	<a href={entry.link} target="_blank" rel="noopener noreferrer"> {entry.value} </a>
								:	entry.value
							}
						</td>
					</tr>
				)
			} else {
				return (
					<tr key={index}>
						<td colSpan={2} style={{textAlign: "center"}}> <b><i>{entry.value}</i></b> </td>
					</tr>
				)
			}
		})}
		</>
	)
}

const request = {
	timeout: null,
	start: null,
	result: {status: 0, message: "no requests"},
	onFinish: null,
	reset: function() {
		if (this.timeout) {
			clearTimeout(this.timeout);
			this.timeout = null;
		}
	},
	doTimeout: function(onFinish) {
		if (this.timeout) {
			this.result.status = "";
			this.result.message = `server answer timeout ${Date.now() - this.start} ms`;
			this.reset();
			this.onFinish();
		}
	},
	doSuccess: function() {
		if (this.timeout) {
			this.reset();
			this.result.status = 200;
			this.result.message = `data received in ${Date.now() - this.start} ms`;
			this.onFinish();
		};
	},
	get: function (timeout, onFinish, dataOwner, getData) {
			this.result = {status: 200, message: `data querying...`};
			this.onFinish = onFinish;
			this.timeout = setTimeout(this.doTimeout.bind(this), timeout);
			this.start = Date.now();
			getData(dataOwner, this.timeout, this.doSuccess.bind(this), this.result);
	}
}

function getObjInfo(object, notTimeoutOver, onSuccess, result) {
	let info = [];
	const MS_PER_DAY = 1000 * 60 * 60 * 24;
	let dt0 = (new Date(new Date().getFullYear(), 0, 1)).valueOf();
	
	info.push( {item: "", value: "location"} );
	info.push( {item: "post address", value: object.post_address} );
	info.push( {item: "longtude", value: object.coord.lon} );
	info.push( {item: "lattitude", value: object.coord.lat} );
	info.push( {item: "installation location", value: `service room, box #${Math.trunc(Math.sin(object.id)**2 * 10 + 1)}`} );
	
	info.push( {item: "", value: "measuring equipment"} );
	info.push( {item: "data controller", value: "NiK KS-02-03", link: "https://nik.net.ua/en/product/controller-data-collection-ks-02-03"} );

	info.push( {item: "serial no.", value: (utils.genRandonInteger(10000, 999999, object.id*3 + 3)).toString().padStart(8, '0')} );
	let dt = utils.genRandomDate(dt0, -7, 4, object.id);
	info.push( {item: "date of manufacture", value: utils.formatDate(dt)} );
	let dt_inst = utils.genRandomDate(dt, 0, 1, 2*object.id);
	info.push( {item: "date of installation", value: utils.formatDate(dt_inst)} );
	info.push( {item: "seal no.", value: utils.genRandonInteger(100, 999, object.id*3 + 3)} );
	dt = utils.genRandomDate(Date.now(), 0, 1, 2*object.id);
	if (Math.ceil((dt - Date.now()) / MS_PER_DAY) < 30) {
		info.push( {item: "next verification date", value: utils.formatDate(dt), note: "warning"} )
	} else {
		info.push( {item: "next verification date", value: utils.formatDate(dt)} )
	};
	info.push( {item: "channel", value: `${object.rs485Count} x RS-485`} );
	info.push( {item: "meters", value: object.metersCount} );

	info.push( {item: "", value: "communication equipment"} );
	info.push( {item: "communication controller", value: "NiK KK-01-10", link: "https://nik.net.ua/en/product/switching-controller-kk1-10"} );
	info.push( {item: "serial no.", value: (utils.genRandonInteger(10000, 999999, object.id*7 + 7)).toString().padStart(8, '0')} );
	dt = utils.genRandomDate(dt0, -8, 3, object.id);
	info.push( {item: "date of manufacture", value: utils.formatDate(dt)} );//info.push( {item: "date of manufacture", value: utils.formatDate(Date.now() - (3 + Math.sin(2*object.id)) * year)} );
	info.push( {item: "date of installation", value: utils.formatDate(dt_inst)} );//info.push( {item: "date of installation", value: utils.formatDate(Date.now() - (2 + Math.sin(2*object.id)) * year)} );
	info.push( {item: "seal no.", value: utils.genRandonInteger(100, 999, object.id*7 + 7)} );
	dt = utils.genRandomDate(Date.now(), 0, 2, 3*object.id);
	if (Math.ceil((dt - Date.now()) / MS_PER_DAY) < 30) {
		info.push( {item: "next verification date", value: utils.formatDate(dt), note: "warning"} )
	} else {
		info.push( {item: "next verification date", value: utils.formatDate(dt)} )
	};
	info.push( {item: "channel", value: `${Math.trunc(10 * Math.sin(object.id)**2) % 2 ? "GSM/GPRS" : "GSM/Internet"}`} );

	info.push( {item: "", value: "servicing"} );
	info.push( {item: "company", value: "ObolonEnergoService"} );
	info.push( {item: "address", value: "16/2, Dobryninskaya street, Obolonskyi district, Kyiv, 04176, Ukraine"} );
	info.push( {item: "phone no.", value: "38 (044) 123-45-67"} );
	utils.sleep(2500).then( () => {
			if (notTimeoutOver) {
				data.objectInfo.obj = object;
				data.objectInfo.items = info;
				data.objectInfo.time = Date.now();
				result.status = 200;
				result.message = "";
				onSuccess();
			}
		}
	)
}

//-----------------------------------------------------------------------------------------------------------
const ModalObjectInfo = React.forwardRef( (props, ref) => { // onHide, setStatus
		const [loading, setLoading] = React.useState(false);
		const [objectInfo, setobjectInfo] = React.useState( {show: false, idxObj: -1} );
		React.useImperativeHandle(ref, () => (
				{ setShowObjectInfo: (value) => setobjectInfo( value ) } // value: {show, idxObj}
			)
		);

		let obj = objectInfo.idxObj >= 0 && objectInfo.idxObj < data.objects.length 
				? data.objects[objectInfo.idxObj] 
				: null;
		let obj_new = obj !== data.objectInfo.obj;

		const refTable = React.useRef(null);

		React.useEffect(	() => {
								if (obj) {
									if (loading) 
										props.setStatus("requesting object info ...")
									else {
										props.setStatus("");
										if (refTable.current)
											refTable.current.focus();
									}
								}
							}, [loading]); 
		if (obj && obj_new && !loading) {
			setLoading(true);
			return null;
		};

		const nItems = data.objectInfo.items.length > 0 ? data.objectInfo.items.length : 25;
		if (obj) {
			if (loading) {
				request.get(5000, ()=>setLoading(false), obj, getObjInfo);
			} else {
				request.result = {status: 200, message: "data has been received"};
			}
			return (
				<Modal 	show={objectInfo.show} onHide={props.onHide}
						size="lg" aria-labelledby="contained-modal-title-vcenter" centered scrollable 
						dialogClassName="modal-50w" fullscreen="lg-down" _container={props.container}>
					<Modal.Header closeButton>
						<Modal.Title id="contained-modal-title-vcenter">
							{obj.name}
						</Modal.Title>
					</Modal.Header>
					<Modal.Body>
					{request.result.status === 200 &&
							<Table size="sm" tabIndex="0" ref={refTable}>
								<tbody>
									{(loading)
										?	<TablePlaceHolder rows={nItems}/>
										:	<TableInfo/>
									}
								</tbody>
							</Table>
						}
						{request.result.status !== 200 &&
							request.result.message						
						}
					</Modal.Body>
					<Modal.Footer style={{justifyContent: "space-between"}}>
						{(loading)
							?	<>
								<span style={{marginLeft_: "10px"}}><i>connecting server...</i></span>
								<Spinner animation="border" variant="primary" size="lg"/>
								</>
							:	<>
								{request.result.status === 200
									? 	<span><i> data received on {utils.formatDateTime(data.objectInfo.time)} </i></span>
									: 	<span><i> 
											{request.result.status === ""
												?	"server not answering"
												: 	"HTTP status: " + request.result.status 
											}
										</i></span>
								}
								<Button className="btn" style={{align: "left"}} variant="outline-light" size="sm" 
										title="refresh data" aria-label="refresh" onClick={()=>setLoading(true)} >
									<img src={process.env.PUBLIC_URL + '/img/refresh_gray.svg'} alt="refresh"/>
								</Button>
								</>
						}
					</Modal.Footer>
				</Modal>
			);
		} else {
			return null
		}
	}
)
