import React, { Component } from "react";
import Offcanvas from "react-bootstrap/Offcanvas";
import Instruments from './Instruments.js';
import ObjectList from './ObjectList.js';
import {cfg} from './Cfg.js';

export default class SlidePanel extends Component {
    constructor(props) {
		super(props);
		this.state = {show: false};
		this.onClose = this.onClose.bind(this);
		this.onObjChanged = this.props.onObjChanged.bind(this);
	 	this.showObjectInfo = this.props.showObjectInfo.bind(this);
		this.onSelectTab = this.props.onSelectTab.bind(this);
		this.onViewChanged = this.props.onViewChanged.bind(this);
		this.onSortChanged = this.props.onSortChanged.bind(this);
		this.onFilterChanged = this.props.onFilterChanged.bind(this);
		this.onSizeChanged = this.props.onSizeChanged.bind(this);
	}
	show() {
		this.setState({show: true});
	}
	onClose(e) {
		this.setState({show: false});
	}
	render() {
		let show = this.state.show;
		return(
			<Offcanvas id="slide-panel-left" show={show} onHide={this.onClose}>
				<Offcanvas.Header closeButton>
					<Offcanvas.Title>
						{	(cfg.currentTabIdx === 0) &&
							"Object List"
						}
						{	(cfg.currentTabIdx === 1) &&
							"View Settings"
						}
					</Offcanvas.Title>
				</Offcanvas.Header>
				<Offcanvas.Body	style={{padding: "0"}}>
					{	(show && cfg.currentTabIdx === 0) &&
						<ObjectList id="object-list"	
										onChanged={this.onObjChanged} 	showObjectInfo={this.showObjectInfo}
										onSelectTab={this.onSelectTab}	/>
					}
					{	(show && cfg.currentTabIdx === 1) &&
						<Instruments 	onSortChanged={this.onSortChanged} 		onSizeChanged={this.onSizeChanged}
										onViewChanged={this.onViewChanged}		onObjectInfo={this.showObjectInfo}
										onFilterChanged={this.onFilterChanged} 	filter={this.props.filter}	/>
					}
				</Offcanvas.Body>
			</Offcanvas>
		)
	}
}


