import React from 'react';
import './AppMeters.css';
import { ToastContainer, Flip } from 'react-toastify';//https://fkhadra.github.io/react-toastify/introduction
import "react-toastify/dist/ReactToastify.css";
import {cfg, appTitle} from "./Cfg.js";
import {data} from './Data.js';
//import Meter from './Meter.js';
import NavBar from './NavBar.js';
import MeterCards from './MeterCards.js';
import MeterBars from './MeterBars.js';
import SlidePanelLeft from './SlidePanelLeft.js';
import SlidePanelRight from './SlidePanelRight.js';
import Instruments from './Instruments.js';
import ObjectList from './ObjectList.js';
import LlMap from './LlMap.js';
import ModalUserAuth from './ModalUserAuth.js';
import ModalObjectInfo from './ModalObjectInfo.js';
import {NetOperation} from './NetOperation.js';
//import {UserInactivityStatus, UserInactivityProgress, UserInactivityToast} from './UserInactivityIndicator.js';
import {UserInactivityToast} from './UserInactivityIndicator.js';
import {utils} from './Utils.js';
import { ModalUserInfo } from './ModalUserInfo';

function AppMeters(props) {
		const refBadWindow = React.useRef(false);
		const [badWindow, setBadWindow] = React.useState(false);
		const [instrumentsDisabled, setInstrumentsDisabled] = React.useState(false);
		const [filter, setFilter] = React.useState("");
		const [mapTooltips, setMapTooltips] = React.useState(true);
		//const [objectInfo, setObjectInfo] = React.useState( objObjectInfo(-1) );
		//const objectInfo = null; //  !!	
		const [statusMessage, setStatusMessage] = React.useState("");
		const [netOperation, setNetOperation] = React.useState( {procedure: null, onFinish: null, onError: null, setStatus: null} );
		const [metersState, setMetersState] = React.useState( {unflipAll: false, closeAll: false} );
		// eslint-disable-next-line
		const [_, forceUpdate] = React.useReducer(x=>!x, true);
		//const [userTimeout, setUserTimeout] = React.useState( {now: 0, max: 60} );
		const userTimeout = React.useRef( {now: 0, max: 60} );

		const refSlidePanelLeft = React.useRef(null);
		const refSlidePanelRight = React.useRef(null);

		React.useEffect( () => {
				cfg.init();
				onResized();
				cfg.sizeTileIdx = Math.min(1, cfg.sizes.widthIdx);
				window.addEventListener("resize", onResized);
				return( () => {
					window.removeEventListener("resize", onResized);
				})
			}, [] // once upon start
		)

		function onResized() {
			let errWindowSize = window.innerWidth < 340 || window.innerHeight < 600;
			if (!refBadWindow.current && errWindowSize) {
				refBadWindow.current = true;
				setBadWindow(true);
				props.pauseTimeout();
			} else {
				if (refBadWindow.current && !errWindowSize) {
					refBadWindow.current = false;
					setBadWindow(false);
					props.resumeTimeout( userTimeout.current.now, callbackUserTimeout );
				}
				let widthIdx = cfg.sizes.widthCard.reduce( (result, value, index) => {
						let widthLeftPanel = window.innerWidth < 576 ? 0 : cfg.sizes.widthLeftPanel;
						return window.innerWidth - widthLeftPanel - 50 > value ? index : result;
					}, 0);
				if (cfg.sizes.widthIdx !== widthIdx) {
					cfg.sizes.widthIdx = widthIdx;
					// let doChange = (window.innerWidth < 576);
					// if (doChange) {
					// 	//cfg.sizeTileIdx = widthIdx;
					// }
					if (cfg.sizes.onWidthIdxChanged) {
						cfg.sizes.onWidthIdxChanged(widthIdx, window.innerWidth < 576);
					}
				}
			}
		}

		function onLogin(fOnLogin, fOnAfterLoginError) {
			const doOnLogin = () => {
					props.startTimeout( callbackUserTimeout );
					if (fOnLogin)
						fOnLogin();
					forceUpdate();
				};
			const doOnAfterLoginError = () => {
					props.pauseTimeout( callbackUserTimeout );
					if (fOnAfterLoginError)
						fOnAfterLoginError();
				};
			if (data.objects.length === 0) {
				getObjects(doOnLogin, doOnAfterLoginError);
			} else {
				doOnLogin();
			};
		}

		function getObjects(doOnLogin, doOnError) {
			setStatusMessage("requesting available objects ...");
			setNetOperation(	{	procedure: data.getObjects, 
									onFinish:	() => {
											doOnLogin();
											onGetObjects();
										},
									onError:	() => {
											doOnError();
										},
									setStatus: setStatus
								} 
			);
		}
		function onGetObjects() {
			utils.toastSuccess("object available: " + data.objects.length)		
			setStatusMessage("");
			setNetOperation({procedure: null, onFinish: null, setStatus: null});
		}
		
		//const refUserInactivityStatus = React.useRef(null);
		//const refUserInactivityProgress = React.useRef(null);
		const refUserInactivityToast = React.useRef(null);
		
		function callbackUserTimeout(timeout, timeoutLast) {
			userTimeout.current = {now: timeout, max: timeoutLast};
			//refUserInactivityStatus.current.setProgress( userTimeout.current );
			//refUserInactivityProgress.current.setProgress( userTimeout.current );
			refUserInactivityToast.current.setProgress( userTimeout.current );
			if (timeout <= 0) {
				showObjectInfo(false);
				showUserInfo(false);
				forceUpdate();
			}
		}

		const setStatus = React.useCallback( (message) => {
				setStatusMessage(message);
			}, [statusMessage]
		)

		function objObjectInfo(idx) {
			return idx >= 0 ? {show: true, idxObj: idx} : {show: false, idxObj: -1};
		}
		const refModalObjectInfo = React.useRef(null);
		const showObjectInfo  = React.useCallback( (idx) => {
				refModalObjectInfo.current.setShowObjectInfo( objObjectInfo(idx) );
			}, [cfg.currentObj]
		)
		function onHideObjectInfo(idx) {
			refModalObjectInfo.current.setShowObjectInfo( objObjectInfo(-1) );
		}

		const refModalUserInfo = React.useRef(null);
		const showUserInfo  = React.useCallback( (show) => {
				refModalUserInfo.current.showUserInfo(show);
			}, []
		)

		const onBtnOffcanvasObjectListOrInstruments = React.useCallback( () => {
				refSlidePanelLeft.current.show(); 
			}, [cfg.currentObj]
		)

		const onBtnOffcanvasTogglerMapOrObject  = React.useCallback( () => {
				refSlidePanelRight.current.open(); 
			}, [cfg.currentObj]
		)

		const onObjChanged = React.useCallback( (object) => {
				setFilter("");
				setMapTooltips(false);
				setTimeout(	() => {
						setMapTooltips(true);
						setStatusMessage("");
					}, 0
				);
			}, [cfg.currentObject, data.objects]
		)
		
		const onSelectTab = React.useCallback( () => {						//   <-> 
				refSlidePanelLeft.current.setState({show: false});
				if (cfg.currentTabIdx === 1)		// 
					if (cfg.currentObj === data.meters.obj) {
						forceUpdate(); //setFilter(filter)	
					} else {
						data.resetMeters();
						setStatusMessage("requesting meters data ...");
						setInstrumentsDisabled(true);
						setNetOperation({procedure: data.getMeters, onFinish: onGetMeters, setStatus: setStatus});
					}
				else								// 
					forceUpdate();// setFilter(filter);
			}, [cfg.currentTabIdx]
		)

		const onDataChanged = React.useCallback( () => {	//   
				data.resetData();
				setStatusMessage("requesting meters data ...");
				setMetersState( previousState => {return { ...previousState, unflipAll: true } } );
				setInstrumentsDisabled(true);
				//setUnflipAll(true);
				setNetOperation( {procedure: data.getMeters, onFinish: onGetMeters, setStatus: setStatus} );
				///setNetFinish(onGetMeters);
			}, [cfg.monthIdx]
		)

		function onGetMeters(result) {						//    
			if (result.message)
				utils.toastSuccess("meters available: " + data.meters.items.length)
			data.meters.items.sort( cfg.sortMeters );
			updateMeters();
			setInstrumentsDisabled(false);
		}
	
		const onViewChanged = React.useCallback( () => {
				onSortChanged(false);
			}, [cfg.viewIdx]
		)

		const onSortChanged = React.useCallback( (reSort = true) => {
				if (cfg.viewIdx === 0 && reSort) {
					setStatusMessage("sorting ...");
					setMetersState( previousState => {return { ...previousState, closeAll: true } } );
					//setCloseAll(true); 	
					setTimeout(	
						() => {
							setTimeout( 
								() => {
									data.meters.items.sort( cfg.sortMeters );
									setMetersState( {unflipAll: true, closeAll: true} );
									//setCloseAll(true); setUnflipAll(true);
									setTimeout(
										() => {
											setTimeout(updateMeters, 500)
										}, 0 
									); 
								}, 500
							);
						}, 0 
					)
				} else if (cfg.viewIdx === 1 && reSort) {
					setStatusMessage("sorting ...");
					data.onMetersChanged(true,
										() =>	{
											setTimeout( 
												() => 	{	
													data.meters.items.sort( cfg.sortMeters );
													data.onMetersChanged(false, 
																		() =>	{
																			setTimeout(() => setStatusMessage(""), 500)
																		}
													);
												}, 
												1000
											);
										}
					);
				} else if (cfg.viewIdx === 1 || !reSort) {
					setStatusMessage("sorting ...");
					setMetersState( previousState => {return { ...previousState, unflipAll: true } } );
					//setUnflipAll(true);
					setTimeout( () => {
									if (reSort)
										data.meters.items.sort( cfg.sortMeters ); 
									updateMeters();
								}, 0
					)			
				}
			}, [cfg.sortKindIdx, cfg.sortAsc]
		)
		
		const onFilterChanged = React.useCallback( (f) => {
				cfg.filter = f;
				if (cfg.viewIdx === 0) {
					setFilter(f);
					setTimeout( () => {updateMeters()}, 0);
				} else if (cfg.viewIdx === 1) {
					data.onMetersChanged(false);
					//this.onSortChanged(true);
				} else {};
			}, [cfg.filter]
		)

		const onSizeChanged = React.useCallback( (sizeIdx) => {
				setMetersState( previousState => {return { ...previousState, closeAll: true } } );
				setTimeout(	
					() => {
						setTimeout( 
							() => {
								cfg.sizeTileIdx = sizeIdx;
								setMetersState( {closeAll: true} );
								setTimeout(
									() => {
										setTimeout(updateMeters, 500)
									}, 0 
								); 
							}, 500
						);
					}, 0 
				)
				//forceUpdate();
			}, [cfg.sizeTileIdx]
		)

		function updateMeters() {
			if (cfg.viewIdx === 0) {
				setFilter(filter);
				setStatusMessage("");
				setNetOperation({procedure: null, onFinish: null, setStatus: null});
				//setNetFinish(null);
				setMetersState( {unflipAll: false, closeAll: false} );
				//setUnflipAll(false); setCloseAll(false);
			} else if (cfg.viewIdx === 1) { //if MeterBars need to be updated
				setFilter(filter);
				setStatusMessage("");
				setNetOperation({procedure: null, onFinish: null, setStatus: null});
				setMetersState( {unflipAll: false, closeAll: false} );
				//setUnflipAll(false);	setCloseAll(false);
	/*
				cfg.viewIdx = 2; // 1st delete MeterBars
				this.setState( {filter: this.state.filter, statusMessage: "", netOperation: null, netFinish: null, unflipAll: false, closeAll: false}, 
								() => {
									cfg.viewIdx = 1; //2nd recreate MeterBars
									this.setState( {filter: this.state.filter} )
								}
							)
	*/
			}
		}
		
		const refContent = React.useRef(null);
		
		if (badWindow) {
			return (
				<div style={ {position: "fixed", top: "50%", left: "50%", transform: "translate(-50%, -50%)"} }>
					<p>Window size is not enough for this application</p>
				</div>
			)
		}

		let mainClass = (cfg.currentTabIdx === 1) ? "main-cards" : "main-map";
		let emWidth = utils.pxToEm(window.innerWidth, document.getElementById("main"));
		const emMax = 57;
		let toastAboutWidth = "100%";
		if (emWidth > emMax) {
			toastAboutWidth = emMax + "em";//100*emMax/emWidth + "%";
		}
		
		// let showForBadWindow = (badWindow) ? {display: "block", zIndex: "2000"} : {display: "none"};
		// // let hideForBadWindow = (badWindow) ? {display: "none"} : {display: "block"};
		// <div style={ {...showForBadWindow, ...{position: "fixed", top: "50%", left: "50%", transform: "translate(-50%, -50%)"}} } >
		// 		 	<p>Window size is not enough for this application</p>
		// 		</div>
		// 		<div style={hideForBadWindow}></div>
		return (
			<>
				<NavBar title={appTitle} 
						onBtnOffcanvasObjectListOrInstruments={onBtnOffcanvasObjectListOrInstruments}
						onBtnOffcanvasTogglerMapOrObject={onBtnOffcanvasTogglerMapOrObject}
						onSelectTab={onSelectTab} disabled={instrumentsDisabled}
						showUserInfo={showUserInfo}
				/>
				<div id="content" ref={refContent} >
					<div id="left" className="not-for-sm">
						{	(cfg.currentTabIdx === 0) &&
							<ObjectList 	onChanged={onObjChanged} showObjectInfo={showObjectInfo}
											onSelectTab={onSelectTab}	
							/>
						}
						{	(cfg.currentTabIdx === 1) &&
							<Instruments 	onViewChanged={onViewChanged} onSortChanged={onSortChanged} 
											onSizeChanged={onSizeChanged} onObjectInfo={showObjectInfo}
											onFilterChanged={onFilterChanged} _filter={filter}
									 		onDataChanged={onDataChanged} disabled={instrumentsDisabled}
							/>
						}
					</div>
					
					<div id="main" className={mainClass}>
						{	(cfg.currentTabIdx === 0) && //null
							 <LlMap tooltips={mapTooltips} onChanged={onObjChanged}/>
						}
						{	(cfg.currentTabIdx === 1) &&
							cfg.viewIdx === 0
								? 	<MeterCards filter={filter} setStatus={setStatusMessage} 
												metersState={metersState} cardSize={cfg.sizeTileIdx} />
								: 	(cfg.viewIdx === 1) 
									? 	<MeterBars 	filter={filter} netOperation={netOperation.procedure}
													metersState={metersState} /> 
									: 	null
						}
					</div>
					<ModalUserAuth		show={userTimeout.current.now <= 0} 
										onLogin={{procedure: onLogin, message: "requesting objects available"}}
					/>
					<ModalObjectInfo 	ref={refModalObjectInfo}	container={refContent.current}
										onHide={onHideObjectInfo}	setStatus={setStatusMessage}
					/>
					<ModalUserInfo		ref={refModalUserInfo}	/>
					<NetOperation		params={netOperation}	statusMessage={statusMessage}/>
				</div>
				<div id="btm" className="bg-secondary">
					{/* <UserInactivityProgress 	ref={refUserInactivityProgress} /> */}
					<Status message={statusMessage}/>
					{/* <UserInactivityStatus 	ref={refUserInactivityStatus} /> */}
				</div>
				<SlidePanelLeft	ref={refSlidePanelLeft} 			title={appTitle} 
								onObjChanged={onObjChanged} 		showObjectInfo={showObjectInfo}
								onSelectTab={onSelectTab}			onViewChanged={onViewChanged}
								onSortChanged={onSortChanged} 		onSizeChanged={onSizeChanged}
								onFilterChanged={onFilterChanged} 	filter={filter}
				/>
				<SlidePanelRight	ref={refSlidePanelRight} 		title={appTitle} 
									onSelectTab={onSelectTab}		onViewChanged={onViewChanged}
									disabled={instrumentsDisabled}
									showUserInfo={showUserInfo}
				/>
				<ToastContainer hideProgressBar closeOnClick transition={Flip} 
								enableMultiContainer containerId={'info'}
				/>
				<ToastContainer hideProgressBar closeOnClick transition={Flip} 
								enableMultiContainer containerId={'about'} style={{width: toastAboutWidth}}
								toastStyle_={{backgroundColor: "rgba(150, 150, 150, 0.85)"}}
				/>
				<UserInactivityToast	ref={refUserInactivityToast}
				/>
			</>
		);
}

const Status = React.memo(
	function Status(props) {
		if (props.message === "") 
			return	(<span className="statusPassive"> demo mode: all the data are emulated </span>)
		else	
			return	(<span className="statusActive"> {props.message} </span>);
	}
)

export default AppMeters;
