import React from "react";
import Dropdown from 'react-bootstrap/Dropdown';
import DropdownButton from 'react-bootstrap/DropdownButton';
import {cfg, tileSizesArr} from "./Cfg.js";

function DropDownSize(props)  { // onChanged, disabled
	const [sizeIdx, setSizeIdx] = React.useState(cfg.sizes.widthIdx);

	function onSelect(e) {
		let item = e.target.text;
		if (item !== tileSizesArr[cfg.sizeTileIdx]) {
			let newIdx = tileSizesArr.indexOf(item);
			if (newIdx < 0)
				alert("Error in component DropDownSize: error selected value " + e)
			else {
				if (props.onChanged === undefined)
					alert("Error in component DropDownSize: undefined callback function onChanged")
				else {			
					props.onChanged(newIdx);
				};
			};
		};
	}
    
	function sizesChanged(sizeIdxMax, doChange = false) {
		setSizeIdx(sizeIdxMax);
		if (sizeIdx > sizeIdxMax || doChange) {
			props.onChanged(sizeIdxMax);
		}
	}
	cfg.sizes.onWidthIdxChanged = sizesChanged;

	let caption = "Card Size: " + tileSizesArr[cfg.sizeTileIdx];
	let disabled = (cfg.viewIdx !== 0);
	return (
		<DropdownButton title={caption} variant="secondary" menuVariant="dark" 
						className="d-grid gap-2"
						disabled={props.disabled || disabled} >
			{tileSizesArr.map(function(size, idx) {
				var active = (size === tileSizesArr[cfg.sizeTileIdx]);
				return (
					<Dropdown.Item 	key={size} eventKey={size} active={active} 
									disabled={idx > cfg.sizes.widthIdx}
									onClick={onSelect}>
						{size}
					</Dropdown.Item>
				)
			})}
		</DropdownButton>
	)
}

//export default React.memo(DropDownSize);
export default DropDownSize;