import React, { Component } from 'react';
import Spinner from 'react-bootstrap/Spinner';
import {cfg, tileSizesCla} from "./Cfg.js";
import {data} from './Data.js';
import TableMeterEnergy from "./TableMeterEnergy.js";
import TableInfo from "./TableInfo.js";
import {NetOperation} from './NetOperation.js';
//import {Toast} from 'bootstrap'; <- sample: https://codepen.io/nishant88/pen/yYyePL
//import { toast } from "react-toastify";
//import "react-toastify/dist/ReactToastify.css";
import {utils} from './Utils.js';

class CardMeter extends Component {
    constructor(props) {
		super(props);
		this.onMouseOver = this.onMouseOver.bind(this);
		this.onMouseOut = this.onMouseOut.bind(this);
		this.onClick = this.onClick.bind(this);
		this.onSglClick = this.onSglClick.bind(this);
		this.onDblClick = this.onDblClick.bind(this);
		this.onGetInfo = this.onGetInfo.bind(this);
		this.doubleClickTimeout = 250; // default doubleclick timeout
		this.clickedTimeout = null;
		this.flipped = React.createRef();
		this.flipped.current = false;
		this.state = {mouseOver: false};
	}
	render() {
		var meter = this.props.meter;
		let badge = null;
		let variant = 'secondary';
		if (meter.title) {
			badge = meter.title;
			var badgeClass = this.state.mouseOver ? 'badge bg-primary' : 'badge bg-secondary';
			variant = this.state.mouseOver ? 'primary' : 'secondary';
		};
		this.flipped.current = this.props.unflip ? false : this.flipped.current;
		var cardStyle = this.props.close
							?	'card closed ' + tileSizesCla[cfg.sizeTileIdx]
							:	(this.flipped.current ? 'card flipped' : 'card') + ' ' + tileSizesCla[cfg.sizeTileIdx];
		let netOperation = {procedure: this.state.netOperation, onFinish: this.onGetInfo, setStatus: this.state.setStatus, dataRef: meter};
		return(
			<div 	className={cardStyle} 			
					onMouseOver={this.onMouseOver} 	onMouseOut={this.onMouseOut} 
					onClick={this.onClick} 			onDoubleClick={this.onDblClick} >
				<div className="face front"> 
					<div className="inner">   
						{ badge &&
							<div className="meter-badge text-right">
								<span className={badgeClass}> {badge} </span>
							</div>
						}
						{ meter.energy[0] &&
							<div className="meter-content">
								<TableMeterEnergy meter={meter}/>
							</div>
						}
						{ !meter.energy[0] &&
						    <Spinner 	animation="border" variant={variant}
										style={	{margin: "auto", position: "absolute", 
												 top: "0", bottom: "0", left: "0", right: "0"} }/>
						}
					</div>
				</div> 
				<div className="face back"> 
					<div className="inner">   
						{ badge &&
							<div className="meter-badge text-left">
								<span className={badgeClass}> {badge} </span>
							</div>
						}
						{ meter.info &&
							<div>
								<TableInfo source={meter.info}/>
							</div>
						}
						{ !meter.info &&
						    <Spinner 	animation="border" variant={variant} 
							style={	{margin: "auto", position: "absolute", 
									 top: "0", bottom: "0", left: "0", right: "0"} }/>
						}
					</div>
				</div>
				<NetOperation	params={netOperation} statusMessage={this.state.statusMessage}/>
			</div>
		);
	}
	onMouseOver(e) {
		this.setState({mouseOver: true});
	}
	onMouseOut(e) {
		this.setState({mouseOver: false});
	}
	onClick(e) {
		if (!this.clickedTimeout && !this.state.netOperation) {
			this.clickedTimeout = setTimeout(this.onSglClick, this.doubleClickTimeout);
		}
	}
	onGetInfo(result) {
		this.setState( {statusMessage: "", netOperation: null, setStatus: null, netFinish: null} );
		this.props.setStatus("");
	}
	onSglClick(e) {
		this.clickedTimeout = null;
		this.flipped.current = !this.flipped.current;
		/*
		this.setState( this.state );
		if (this.flipped.current)
			setTimeout( this.onSglClick, 1000 );
		*/
		if (this.flipped.current && !this.props.meter.info) {
			this.props.setStatus("requesting meter's info ...");
			this.setState( {statusMessage: "requesting meter's info ...",
							netOperation: data.getInfo, setStatus: this.props.setStatus, 
							netFinish: this.onGetInfo} );
		} else {
			this.setState( this.state );
			//setTimeout( this.onSglClick(e), 2000 );
		}
	}	
	onDblClick(e) {
		e.preventDefault();
		clearTimeout(this.clickedTimeout);
		this.clickedTimeout = null;
		notify(`dbl click on "${this.props.meter.title}"`);
	}	
}

const notify = (text) => {
  	utils.toastError(text);
};

export default CardMeter;

