import React from 'react';

export default function UserActivity(WrappedComponent, timeoutMinutes, timeoutLast = 1) {
    return function(props) {
        let timer = null;
        let cTimeout = timeoutMinutes * 60  + 2;
        let cTimeoutLast = timeoutLast * 60;
        let timeout = 0;
        let setLastTimeout = null;
        
        React.useEffect( () => {
                return ( () => {
                    if (timer)
                        clearInterval(timer);
                    }
                )
            }
        )
        function callbackTimeout(timeout) {
            if (setLastTimeout) {
                setLastTimeout(timeout, cTimeoutLast);
            }
        }
        function doTimeout() {
            if (timeout === Infinity)
                return;
            timeout--;
            if (timeout <= 0) {
                clearInterval(timer);
                timer = null;
                callbackTimeout(0);
            } else if (timeout > cTimeoutLast) {
                ; //this.getTimeout(Infinity)
            } else {
                callbackTimeout(timeout);
            }
        }
        function resetTimeout(event) {
            if (timer) {
                let b = (timeout <= cTimeoutLast);
                timeout = cTimeout;
                if (b)
                    callbackTimeout(Infinity);
            }
        }
        function startTimeout(fSetLastTimeout) {
            if (!timer) {
                timeout = cTimeout;
                setLastTimeout = fSetLastTimeout;
                timer = setInterval(doTimeout, 1000);
                callbackTimeout(Infinity);
            }
        }
        function pauseTimeout(fSetLastTimeout) {
            timeout = Infinity;
            setLastTimeout = fSetLastTimeout;
            callbackTimeout(Infinity);
        }
        function resumeTimeout(aTimeout = cTimeout, fSetLastTimeout) {
            if (timer) {
                timeout = aTimeout;
                setLastTimeout = fSetLastTimeout;
                //callbackTimeout(timeout);
            }
        }

        return(
                <div    onKeyDown={resetTimeout} onMouseMove={resetTimeout} onClick={resetTimeout} 
                        style={{position: "fixed", display: "contents"}}>
                    <WrappedComponent   startTimeout={startTimeout}  pauseTimeout={pauseTimeout} resumeTimeout={resumeTimeout} />
                </div>
        )
    } 
}
//{...this.props}
