import React from 'react';
import {useEffect} from "react";
import './ObjectList.css';
import ListGroup from "react-bootstrap/ListGroup";
import Dropdown from "react-bootstrap/DropDown";
import {data} from "./Data.js";
import {cfg} from "./Cfg.js";
import {mapWnd, showTooltip, hideTooltip} from "./LlMap.js";

function ObjectList(props) { // onChanged, onSelectTab, showObjectInfo
	// eslint-disable-next-line
	const [ignored, forceUpdate] = React.useReducer(x => x + 1, 0);

	function clickObject(object) {
		if (cfg.currentObject !== object || mapWnd.outOf(cfg.currentObject.coord)) {
			if (props.onChanged) {
				//setSelectedObj(object);
				cfg.currentObject = object;
				forceUpdate();
				props.onChanged(object)
			} else {
				alert("Error in component ObjectList: undefined callback function onChanged");
			}
		}
	};
	// for tabbing and hinting
	const refItems = React.useRef([]);
	if (refItems.current.length !== data.objects.length) {
        refItems.current = data.objects.map((_, i) => refItems.current[i] || React.createRef());
    }
	/*function getIdxByEvent(e) {
		let item = refItems.current.find( (itm) => 
				{return itm.current === e.target} );
		return item ? refItems.current.indexOf(item) : -1;
	};*/
	function hintMarker(e) {
		let idx = Number(e.target.value);//getIdxByEvent(e);
		if (idx >= 0 && data.objects[idx] !== cfg.currentObject)
			showTooltip(idx);
	};
	function unhintMarker(e) { 
		let idx =  Number(e.target.value);//getIdxByEvent(e);
		if (idx >= 0 && data.objects[idx] !== cfg.currentObject)
			hideTooltip(idx);
	};
	function dlgObjectInfo(e) {
		e.preventDefault();
		let idx =  Number(e.target.value);//getIdxByEvent(e);
		if (props.showObjectInfo)
			props.showObjectInfo(idx)
		else 
			alert("Error in component ObjectList: undefined callback function showObjectInfo");
	};
	function clkObjectInfo(obj) {
		let idx = data.objects.indexOf(obj);
		if (idx >= 0) {
			if (props.showObjectInfo)
				props.showObjectInfo(idx)
			else 
				alert("Error in component ObjectList: undefined callback function showObjectInfo");
		};
	};

	//let activeRef = useRef(null);
	
	let activeIdx = -1;
	useEffect(	() => 	{
			//var r = null, x, y; 
			if (cfg.currentObject)
				activeIdx = data.objects.indexOf(cfg.currentObject);
			if (activeIdx >= 0) 
				refItems.current[activeIdx].current.scrollIntoView({behavior: 'smooth', block: 'nearest'});
			/*let e = window.event, mouse = null;
			if (e.pageX && e.pageY) {
				mouse = {x: e.pageX, y: e.pageY};
			};*/
			refItems.current.forEach( 
				(refItem, idx) => {
					if (refItem.current) {
						/*
						r = refItem.current.getBoundingClientRect();
						if (mouse) {
							if (mouse.x >= r.left && mouse.x <= r.right && mouse.y >= r.top && mouse.y <= r.bottom) {
								showTooltip(idx);
							}
						}
						*/
						refItem.current.addEventListener("focus", hintMarker, true); 
						refItem.current.addEventListener("mousemove", hintMarker, true); 
						refItem.current.addEventListener("blur", unhintMarker, true); 
						refItem.current.addEventListener("mouseenter", hintMarker, true); 
						refItem.current.addEventListener("mouseleave", unhintMarker, true);
						refItem.current.addEventListener("dblclick", dlgObjectInfo, true);
					};
				} 
			);
			return function unmount() {
				refItems.current.forEach( 
					(refItem) => {
						if (refItem.current) {
							refItem.current.removeEventListener("focus", hintMarker, true); 
							refItem.current.removeEventListener("mousemove", hintMarker, true); 
							refItem.current.removeEventListener("blur", unhintMarker, true); 
							refItem.current.removeEventListener("mouseenter", hintMarker, true); 
							refItem.current.removeEventListener("mouseleave", unhintMarker, true); 
							refItem.current.removeEventListener("dblclick", dlgObjectInfo, true);
						};
					} 
				)
			};
		}//,	[cfg.currentObject]
	);
	
	return (
		<ListGroup as="dl" style={{minWidth: "-webkit-fill-available"}}>
			{
				data.objects.map( (obj, idx, arr) => {
					let className, hint;
					let active = (obj === cfg.currentObject);//(obj === selectedObj);
					//let ref = null;
					if (active) {
						className = "object-list-item-active";
						hint = `meters: ${obj.metersCount}`;
					} else {
						className = "object-list-item";
						hint = "";
						if (obj.address)
							hint += `${obj.address}`;
						if (obj.metersCount)
							hint += `\nmeters: ${obj.metersCount}`;
					};
					if (active) {
						return(
							<div key={idx} className={className}>
							<ListGroup.Item as="div" id={idx} className="object-list-item-button"
									value={idx}
									active={active}
									title={hint}
									onClick={ () => clickObject(obj) }
									ref={refItems.current[idx]}
									>
								{obj.name}
							<Dropdown className="object-list-item-active-dropdown">
								<Dropdown.Toggle variant="primary" id="dropdown-split-basic"/>
									<Dropdown.Menu className="dropdown-menu">
										<Dropdown.Item onClick={() => clkObjectInfo(obj)}> object info </Dropdown.Item>
										<Dropdown.Item onClick={() => {cfg.currentTabIdx = 1; props.onSelectTab()}}> {"meters: " + obj.metersCount} </Dropdown.Item>
									</Dropdown.Menu>
							</Dropdown>
							</ListGroup.Item>
							{active &&
								<dd style={{textAlign: "right"}}>
									<sup> {obj.address} </sup>
								</dd> 
							}
							</div>
						)
					} else {
						return(
							<div key={idx} className={className}>
							<ListGroup.Item as="button" id="object-list" className="object-list-item-button"
									value={idx}
									active={active}
									title={hint}
									onClick={ () => clickObject(obj) }
									ref={refItems.current[idx]}
									>
								{obj.name + "   "}
							</ListGroup.Item>
							</div>
						)
					}
				} )
			}
		</ListGroup>
	)
}

export default React.memo(ObjectList)
//export default ObjectList
